import React, { useEffect, useState, useRef } from 'react';
import { ZwDivArrow, ZwCanvasQrcode, ZwBarcodeCanvas } from '../base_components/zw_components/zw_components';
import {
    computeAngleIndex,
    makeQrcodeStyle,
    updateQrcodeStyle,
    makeBarcodeStyle,
    updateBarcodeStyle,
    updateBarcodeValues,
    makeBarcode,
} from '../../models/sourceStates';
import { computeElementScreenLocation } from '../../commmon_funcs/h5_element_handle';

import { barcodeTypes } from '../../custom_configs';
import { ZwSvgSpin } from '../../images/svgs/zw_svgs';

const QrcodeDiv = ({ labelObj, setLabelObj, labelObjUpdateCount, setLabelObjUpdateCount, visible, setVisible, visibleNumber, editState }) => {
    const qrDivElement = useRef();

    const [labQRCode, setLabQRCode] = useState({})
    const [qrcodeDivStyle, setQrcodeDivStyle] = useState({});

    const [showQrcode, setShowQrcode] = useState(false);
    const [urlText, setUrlText] = useState('http://localhost:8000/');
    const [qrcodeWidth, setQrcodeWidth] = useState(30);

    const [labelWidth, setLabelWidth] = useState(80);
    const [labelHeight, setLabelHeight] = useState(50);

    const [tipsText, setTipsText] = useState('');
    const [tipsFontSize, setTipsFontSize] = useState(8);

    const [qrcodeEditDivLocation, setQrcodeEditDivLocation] = useState({ top: 20, left: 280 });

    useEffect(() => {
        console.log(labelObj);
        const qrObj = labelObj.labQRCode ? labelObj.labQRCode : {};
        let qrStyle = qrObj.styleQrCode ? { ...qrObj.styleQrCode } : {};
        const showQr = qrObj.showQrcode ? qrObj.showQrcode : false;
        const qrUrl = qrObj.urlValues ? qrObj.urlValues : 'http://localhost:8000/';
        const qrcodeSize = qrObj.qrcodeSize && parseInt(qrObj.qrcodeSize) ? parseInt(qrObj.qrcodeSize) : '';
        const tipsT = qrObj.qrcodeTips ? qrObj.qrcodeTips : '';
        const tipsFontSize = qrObj.tipsFontSize ? qrObj.tipsFontSize : 8;

        const labWidth = labelObj.labWidth ? parseInt(labelObj.labWidth) : 80,
              labHeight = labelObj.labHeight ? parseInt(labelObj.labHeight) : 50;
        
        qrStyle.display = showQr ? 'block' : 'none';

        setLabelWidth(labWidth);
        setLabelHeight(labHeight);
        setTipsFontSize(tipsFontSize);
        setLabQRCode(qrObj);
        setQrcodeDivStyle(qrStyle);
        setShowQrcode(showQr);
        setUrlText(qrUrl);
        setQrcodeWidth(qrcodeSize);
        setTipsText(tipsT);
        console.log(showQr, qrUrl, qrStyle);
    }, [labelObjUpdateCount]);

    const computeQrEditDivLocation = () => {
        const qrDivLocationObj = computeElementScreenLocation(qrDivElement.current);
        const editDivLocationObj = {top: qrDivLocationObj.bottom + 10, left: qrDivLocationObj.right + 10};
        setQrcodeEditDivLocation(editDivLocationObj);
    };

    const clickQrCodeHandle = (e) => {
        // 点击二维码操作
        computeQrEditDivLocation();
        if (editState) {
            setVisible(visibleNumber);
        }
    };

    const updateLabQRCode = (upObj) => {
        // 更新 urlValues
        const upObjKeys = Object.keys(upObj);
        const updateKeys = ['urlValues', 'qrcodeTips', 'styleQrCode', 'qrcodeSize', 'tipsFontSize', 'showQrcode']
        const updateObj = {};
        for (let index = 0; index < upObjKeys.length; index++) {
            const k = upObjKeys[index];
            if (updateKeys.indexOf(k) > -1) {
                if (upObj[k]) {
                    updateObj[k] = upObj[k];
                } else {
                    updateObj[k] = '';
                }
            }
        }
        const labQRCodeObj = { ...labQRCode, ...updateObj };
        setLabQRCode(labQRCodeObj);
        setLabelObj({ ...labelObj, labQRCode: labQRCodeObj });
        setLabelObjUpdateCount(labelObjUpdateCount + 1);
    }

    return (
        <>
            <div style={qrcodeDivStyle} ref={qrDivElement} className="p-0 m-0">
                <ZwCanvasQrcode urlText={urlText} size={qrcodeWidth} onClick={clickQrCodeHandle}></ZwCanvasQrcode>
                <input className="w-full h-auto" style={{fontSize: `${tipsFontSize}pt`}} defaultValue={tipsText} onChange={(e) => updateLabQRCode({qrcodeTips: e.target.value})}></input>
            </div>
            <EditQrcodeModal
                labelObj={labelObj} setLabelObj={setLabelObj}
                updateLabQRCode={updateLabQRCode}
                labelObjUpdateCount={labelObjUpdateCount}
                setLabelObjUpdateCount={setLabelObjUpdateCount}
                qrcodeWidth={qrcodeWidth}
                qrcodeEditDivLocation={qrcodeEditDivLocation}
                visible={visible}
                setVisible={setVisible}
                visibleNumber={visibleNumber}
                labWidth={labelWidth}
                labHeight={labelHeight}
            ></EditQrcodeModal>
        </>
    )
};
QrcodeDiv.defaultProps = {
    labelObj: { labQRCode: {} },
    setLabelObj: (labObj) => console.log(labObj),
    visible: 0,
    setVisible: (num) => console.log(num),
    visibleNumber: 4,
    editState: false,
    // labQRCode: {
    //     delYes: false,
    //     urlValues: "",
    //     qrcodeTips: "二维码提示",
    //     databaseText: "qrcode",
    //     styleQrCode: styleQrCode,
    //     styleTips: styleTips,
    //     showQrcode: false,
    // },
};

const EditQrcodeModal = ({ labelObj, setLabelObj, updateLabQRCode, labelObjUpdateCount, setLabelObjUpdateCount, qrcodeEditDivLocation, visible, setVisible, visibleNumber, labWidth, labHeight }) => {
    // 编辑二维码 uri style
    const [qrcodeObj, setQrcodeObj] = useState({});
    
    const [qrStyle, setQrStyle] = useState({});

    useEffect(() => {
        const labQRCode = labelObj.labQRCode ? labelObj.labQRCode : {};
        setQrcodeObj(labQRCode);
        setQrStyle(labQRCode.styleQrCode ? labQRCode.styleQrCode : makeQrcodeStyle());
    }, [labelObjUpdateCount]);

    const updateCurrentQrcodeStyle = (upObj) => {
        // 更新二维码样式
        const checkUpdateObj = {};
        for (const key in upObj) {
            if (Object.hasOwnProperty.call(upObj, key)) {
                const element = upObj[key];
                if (element) {
                    checkUpdateObj[key] = element;
                } else {
                    checkUpdateObj[key] = '';
                }
            }
        }
        const updatedStyle = updateQrcodeStyle(qrStyle, checkUpdateObj, labWidth, labHeight);
        setQrStyle(updatedStyle);
        setLabelObj({...labelObj, labQRCode: {...qrcodeObj , styleQrCode: updatedStyle}});
        setLabelObjUpdateCount(labelObjUpdateCount + 1);
    }
    
    return (
        <>
            <ZwDivArrow title="二维码编辑" visible={visible} setVisible={setVisible} visibleNumber={visibleNumber} divLocation={qrcodeEditDivLocation} mbNumber={60}>
                <div>
                    <div>
                        <div className="form-control">
                            <label className="label">
                                <span className="label-text">Url</span>
                            </label> 
                            <input type="text" placeholder="请输入url(例如: https://label.zwglass.net/)"
                                value={qrcodeObj.urlValues ? qrcodeObj.urlValues : ''}
                                className="input input-primary input-xs input-bordered"
                                onChange={(e) => updateLabQRCode({urlValues: e.target.value})}
                            />
                        </div>

                        <div className="form-control">
                            <label className="label">
                                <span className="label-text">二维码大小</span>
                            </label> 
                            <input type="text" placeholder="输入数字(比如:50)" 
                                value={qrcodeObj && parseInt(qrcodeObj.qrcodeSize) ? parseInt(qrcodeObj.qrcodeSize) : ''}
                                className="input input-primary input-xs input-bordered"
                                onChange={(e) => updateLabQRCode({qrcodeSize: parseInt(e.target.value)})}
                            />
                        </div>
                        <div className="form-control">
                            <label className="label">
                                <span className="label-text">二维码位置X</span>
                            </label> 
                            <input type="text" placeholder="输入数字(比如:20)" 
                                value={qrStyle.marginLeft && parseInt(qrStyle.marginLeft) ? parseInt(qrStyle.marginLeft) : ''}
                                className="input input-primary input-xs input-bordered"
                                onChange={(e) => updateCurrentQrcodeStyle({marginLeft: parseInt(e.target.value)})}
                            />

                            <label className="label">
                                <span className="label-text">二维码位置Y</span>
                            </label> 
                            <input type="text" placeholder="输入数字(比如:30)" 
                                defaultValue={qrStyle.marginTop && parseInt(qrStyle.marginTop) ? parseInt(qrStyle.marginTop) : ''}
                                className="input input-primary input-xs input-bordered"
                                onChange={(e) => updateCurrentQrcodeStyle({marginTop: parseInt(e.target.value)})}
                            />
                        </div>

                        <div className="form-control">
                            <label className="label">
                                <span className="label-text">二维码提示</span>
                            </label> 
                            <input type="text" placeholder="Tips"
                                defaultValue={qrcodeObj && qrcodeObj.qrcodeTips ? qrcodeObj.qrcodeTips : ''}
                                className="input input-secondary input-xs input-bordered"
                                onChange={(e) => updateLabQRCode({qrcodeTips: e.target.value})}
                            />

                            <label className="label">
                                <span className="label-text">提示文字大小</span>
                            </label> 
                            <input type="text" placeholder="输入数字(比如:9)"
                                defaultValue={qrcodeObj.qrcodeTips ? qrcodeObj.tipsFontSize : ''}
                                className="input input-secondary input-xs input-bordered"
                                onChange={(e) => updateLabQRCode({tipsFontSize: parseInt(e.target.value)})}
                            />
                        </div>
                    </div>
                </div>
            </ZwDivArrow>
        </>
    )
};


const BarcodeDiv = ({ labelObj, setLabelObj, labelObjUpdateCount, setLabelObjUpdateCount, visible, setVisible, visibleNumber, editState }) => {
    // barcode div
    const barDivElement = useRef();

    const [labBarcode, setLabBarcode] = useState({})
    const [barcodeDivStyle, setBarcodeDivStyle] = useState({});

    const [editDivLocation, setEditDivLocation] = useState({ top: 20, left: 280 });
    const [barcodeWidth, setBarcodeWidth] = useState(1);
    const [barcodeHeight, setBarcodeHeight] = useState(30);
    const [barcodeType, setBarcodeType] = useState('');
    const [barcodeText, setBarcodeText] = useState('zwglass.net');


    useEffect(() => {
        // 更新 qrcode 文本 样式
        const barcodeObj = labelObj.labBarcode ? labelObj.labBarcode : makeBarcode();
        console.log(barcodeObj);
        setLabBarcode(barcodeObj);

        const barcodeStyle = barcodeObj.style ? { ...barcodeObj.style } : makeBarcodeStyle();
        barcodeStyle.display = barcodeObj.showCode ? 'block' : 'none';
        setBarcodeDivStyle(barcodeStyle);
        const barcodeSize = barcodeObj.barcodeSize ? barcodeObj.barcodeSize : [1, 30];
        setBarcodeWidth(barcodeSize[0]);
        setBarcodeHeight(barcodeSize[1]);

        const barType = barcodeObj.barcodeType ? barcodeObj.barcodeType : '',
                barText = barcodeObj.urlValues ? barcodeObj.urlValues : 'zwglass.net';
        setBarcodeType(barType);
        setBarcodeText(barText);
    }, [labelObjUpdateCount]);

    const computeQrEditDivLocation = () => {
        const refEditDivLocationObj = computeElementScreenLocation(barDivElement.current);
        const editDivLocationObj = {top: refEditDivLocationObj.bottom + 10, left: refEditDivLocationObj.right + 10};
        setEditDivLocation(editDivLocationObj);
    };

    const clickBarCodeHandle = (e) => {
        computeQrEditDivLocation();
        if (editState) setVisible(visibleNumber);
    }

    return (
        <>
            <div style={barcodeDivStyle} ref={barDivElement} className="p-0 m-0">
                <ZwBarcodeCanvas
                    codeType={barcodeType}
                    text={barcodeText}
                    width={barcodeWidth}
                    height={barcodeHeight}
                    onClick={clickBarCodeHandle}
                ></ZwBarcodeCanvas>
            </div>
            <EditBarcodeModal
                labelObj={labelObj} setLabelObj={setLabelObj}
                labelObjUpdateCount={labelObjUpdateCount}
                setLabelObjUpdateCount={setLabelObjUpdateCount}
                barcodeEditDivLocation={editDivLocation}
                visible={visible}
                setVisible={setVisible}
                visibleNumber={visibleNumber}
            ></EditBarcodeModal>
        </>
    )
};

const EditBarcodeModal = ({labelObj, setLabelObj, labelObjUpdateCount, setLabelObjUpdateCount, barcodeEditDivLocation, visible, setVisible, visibleNumber}) => {
    // 编辑条形码码 text style
    const [barcodeObj, setBarcodeObj] = useState({});
    
    const [barcodeStyle, setBarcodeStyle] = useState({});

    const [labelWidth, setLabelWidth] = useState(80);
    const [labelHeight, setLabelHeight] = useState(50);

    // const [barcodeSize, setBarcodeSize] = useState([2, 30]);
    const [barcodeWidth, setBarcodeWidth] = useState(1);
    const [barcodeHeight, setBarcodeHeight] = useState(20);

    useEffect(() => {
        const labBarCode = labelObj.labBarcode ? labelObj.labBarcode : makeBarcode();
        setBarcodeObj(labBarCode);
        console.log(labBarCode.style);
        setBarcodeStyle(labBarCode.style ? labBarCode.style : makeBarcodeStyle());
        // setBarcodeSize(labBarCode.barcodeSize ? labBarCode.barcodeSize : [2, 30]);
        const barcodeSize = labBarCode.barcodeSize ? labBarCode.barcodeSize : [1, 30];
        setBarcodeWidth(barcodeSize[0]);
        setBarcodeHeight(barcodeSize[1]);

        const labelW = labelObj.labWidth, labelH = labelObj.labHeight;
        setLabelWidth(labelW);
        setLabelHeight(labelH);
    }, [labelObjUpdateCount]);

    const updateLabelAndBarcodeObj = (updatedBarcodeObj) => {
        // 更新条码 object barcodeObj, labelObj
        const copyLabelObj = { ...labelObj, labBarcode: updatedBarcodeObj };
        setBarcodeObj(updatedBarcodeObj);
        setLabelObj(copyLabelObj);
        setLabelObjUpdateCount(labelObjUpdateCount + 1);
    }

    const updateCurrentBarcodeStyle = (upObj) => {
        // 更新二维码样式
        console.log(upObj);
        const barcodeSize = barcodeObj.barcodeSize ? barcodeObj.barcodeSize : [2, 30];
        const updatedStyle = updateBarcodeStyle(barcodeSize, barcodeStyle, upObj, labelWidth, labelHeight);
        console.log(updatedStyle)
        setBarcodeStyle(updatedStyle);
        const updatedBarcode = updateBarcodeValues(barcodeObj, {style: updatedStyle});
        updateLabelAndBarcodeObj(updatedBarcode);              // 更新样式, 更新 labelObj, barcodeObj
    }

    const updateCurrentBarcodeValues = (upObj) => {
        // 更新Barcode 值
        console.log(upObj);
        const updatedBarcodeObj = updateBarcodeValues(barcodeObj, upObj);
        updateLabelAndBarcodeObj(updatedBarcodeObj);
    };

    const updateBarcodeSize = (upObj) => {
        // 更新条码大小
        const updateKeys = ['width', 'height'];
        const updateObj = {width: barcodeWidth, height: barcodeHeight};

        for (const key in upObj) {
            if (Object.hasOwnProperty.call(upObj, key)) {
                const element = parseInt(upObj[key]) ? parseInt(upObj[key]) : '';
                if (updateKeys.indexOf(key) > -1) {
                    updateObj[key] = element;
                }
            }
        }
        const updatedSizeArr = [updateObj.width, updateObj.height];
        updateCurrentBarcodeValues({barcodeSize: updatedSizeArr});
        setLabelWidth(updateObj.width);
        setLabelHeight(updateObj.height);
    };

    const changeAngle = () => {
        // 旋转条形码
        let angleIndex = computeAngleIndex(barcodeStyle) + 1;
        angleIndex = angleIndex < 0 || angleIndex > 3 ? 0 : angleIndex;
        // console.log(angleNum, angleIndex);
        const barcodeSize = barcodeObj.barcodeSize ? barcodeObj.barcodeSize : [2, 30];
        const updatedStyle = updateBarcodeStyle(barcodeSize, barcodeStyle, {}, labelWidth, labelHeight, angleIndex);
        // console.log(updatedInputObj);
        setBarcodeStyle(updatedStyle);
        const updatedBarcode = updateBarcodeValues(barcodeObj, {style: updatedStyle});
        updateLabelAndBarcodeObj(updatedBarcode);              // 更新样式, 更新 labelObj, barcodeObj
    }
    
    return (
        <>
            <ZwDivArrow title="条形码编辑" visible={visible} setVisible={setVisible} visibleNumber={visibleNumber} divLocation={barcodeEditDivLocation} mbNumber={60}>
                <div>
                    <div>
                        <div className="form-control">
                            {/* <div data-tip="旋转条形码" class="tooltip">
                                <button class="btn btn-info btn-circle btn-sm" onClick={changeAngle}>
                                    <ZwSvgSpin width={24} height={24} fill="#F0F0F0"></ZwSvgSpin>
                                </button>
                            </div> */}

                            <select class="select select-bordered select-xs w-full max-w-xs" onChange={e => updateCurrentBarcodeValues({barcodeType: e.target.value})}>
                                <option disabled="disabled" selected="selected" value="">条码类型</option> 
                                {barcodeTypes.map(item => {
                                    return <option value={item.value}>{item.label}</option>
                                })}
                            </select>

                            <label className="label">
                                <span className="label-text">条码编号</span>
                            </label> 
                            <input type="text" placeholder="请输入条码编号(例如: 123456)"
                                defaultValue={barcodeObj.urlValues ? barcodeObj.urlValues : ''}
                                className="input input-primary input-xs input-bordered"
                                onChange={(e) => updateCurrentBarcodeValues({urlValues: e.target.value})}
                            />
                        </div>

                        <div className="form-control">
                            <label className="label">
                                <span className="label-text">条码宽度(一根竖条的宽度)</span>
                            </label> 
                            <input type="text" placeholder="输入数字(比如:2)" 
                                defaultValue={barcodeWidth}
                                className="input input-primary input-xs input-bordered"
                                onChange={(e) => updateBarcodeSize({width: e.target.value})}
                            />
                        </div>
                        <div className="form-control">
                            <label className="label">
                                <span className="label-text">条码高度</span>
                            </label> 
                            <input type="text" placeholder="输入数字(比如:2)" 
                                defaultValue={barcodeHeight}
                                className="input input-primary input-xs input-bordered"
                                onChange={(e) => updateBarcodeSize({height: e.target.value})}
                            />
                        </div>
                        <div className="form-control">
                            <label className="label">
                                <span className="label-text">条码位置X</span>
                            </label> 
                            <input type="text" placeholder="输入数字(比如:20)" 
                                defaultValue={barcodeStyle.marginLeft && parseInt(barcodeStyle.marginLeft) ? parseInt(barcodeStyle.marginLeft) : ''}
                                className="input input-primary input-xs input-bordered"
                                onChange={(e) => updateCurrentBarcodeStyle({marginLeft: parseInt(e.target.value)})}
                            />

                            <label className="label">
                                <span className="label-text">条码位置Y</span>
                            </label> 
                            <input type="text" placeholder="输入数字(比如:30)" 
                                defaultValue={barcodeStyle.marginTop && parseInt(barcodeStyle.marginTop) ? parseInt(barcodeStyle.marginTop) : ''}
                                className="input input-primary input-xs input-bordered"
                                onChange={(e) => updateCurrentBarcodeStyle({marginTop: parseInt(e.target.value)})}
                            />
                        </div>
                    </div>
                </div>
            </ZwDivArrow>
        </>
    )
};

export { QrcodeDiv, BarcodeDiv };
