import React, { useEffect, useState } from 'react';

import { Button, Space} from 'antd';

import EditableTable from '../../customComponents/editableTable';

const indexConvertSphVal = (index) => {
    // index convert sph
    return (0.25 * index).toFixed(2).toString();
};

const queryThincknessAndDiameterItem = (thicknessAndDiameterFormArr, sph) => {
    // 查找 中心厚度和直径表 指定sph的行
    for (let index = 0; index < thicknessAndDiameterFormArr.length; index++) {
        const element = thicknessAndDiameterFormArr[index];
        if (element.sph === sph) {
            return element;
        }
    }
    return {sph: sph, thickness: '1.2', diameter: 72};
};

