const queryThincknessOrDiameter = (thicknessOrDiameterArr, sph, key='thickness') => {
    // 查找中心厚度或直接值 key = 'thickness' or 'diameter'
    const retVal = key === 'thickness' ? '1.2' : 72;
    for (let index = 0; index < thicknessOrDiameterArr.length; index++) {
        const element = thicknessOrDiameterArr[index];
        // console.log(key, sph, element[key]);
        if (element.sph === sph) {
            // console.log(key, sph, element[key]);
            return element[key] ? element[key] : retVal;
        }
    }
    return retVal;
};

const createThicknessDiameterFormArr = (labelObj) => {
    // 创建 中心厚度和直径表 数组 [ {sph: '0', thickness: '2.0', diameter: '72', key: '0'}, ]
    const tDFormArr = [];
    const thicknessDiameterArr = labelObj.thicknessDiameter ? labelObj.thicknessDiameter : [];
    for (let index = 0; index < 41; index++) {
        const currentSph = indexConvertSphVal(index);
        const currentTDFormItem = {
            key: index.toString(),
            sph: currentSph,
            thickness: queryThincknessOrDiameter(thicknessDiameterArr, currentSph, 'thickness'),
            diameter: queryThincknessOrDiameter(thicknessDiameterArr, currentSph, 'diameter'),
        };
        tDFormArr.push(currentTDFormItem);
    }
    return tDFormArr;
};

const LensThicknessDiameterEdit = ({ labelObj, setLabelObj, saveLabelHandle, setVisible}) => {
    /**
     * 中心厚度和直径编辑组件
     */
    const [tableData, setTableData] = useState([]);     // 中心厚度和直径表数据

    useEffect(() => {
        // 给中心厚度直径表格赋值
        // const updated_label_data = createThicknessDiameterFormArr(labelObj);
        // console.log(updated_label_data);
        const TDArray = labelObj.thicknessDiameter ? labelObj.thicknessDiameter : createThicknessDiameterFormArr(labelObj);
        setTableData(TDArray);
    }, [labelObj]);

    const saveHandle = () => {
        // 保存中心厚度编辑
        const updatedLabelObj = { ...labelObj, thicknessDiameter: tableData };
        console.log(updatedLabelObj);
        setLabelObj(updatedLabelObj);
        saveLabelHandle(updatedLabelObj);
        setVisible(0);
    };

    const updateUnderThicknessOrDiameterHandle = (currentItem, thicknessOrDiameterKey, index = -1) => {
        // 批量更新以下中心厚度或直径操作 thicknessOrDiameterKey='thickness' / 'diameter'
        console.log(index);
        const copyTableData = [].concat(tableData);
        const thicknessOrDiameterVal = copyTableData[index][thicknessOrDiameterKey];
        for (let i = index; i < tableData.length; i++) {
            copyTableData.splice(i, 1, { ...copyTableData[i], [thicknessOrDiameterKey]: thicknessOrDiameterVal, }); 
        }
        // console.log(copyTableData);
        // setTableData(copyTableData);
        const updatedLabelObj = { ...labelObj, thicknessDiameter: copyTableData };
        // console.log(updatedLabelObj);
        setLabelObj(updatedLabelObj);
        // saveHandle();
    }

    const tableColumns = [
        { dataIndex: 'sph', fixed: 'left', title: "SPH", width: 80, editable: false, },
        { dataIndex: 'thickness', title: "中心厚度", width: 80, editable: true, inputType: 'input', },
        { dataIndex: 'diameter', title: "直径", width: 80, editable: true, inputType: 'input', },
        { dataIndex: 'multipleUpdateThickness', title: "批量操作", width: 100, editable: false, render: (_, record, index) => {
            return (
                <>
                    <Button type="link" onClick={() => updateUnderThicknessOrDiameterHandle(record, 'thickness', index)}>更新以下厚度</Button>
                    <Button type="link" onClick={() => updateUnderThicknessOrDiameterHandle(record, 'diameter', index)}>更新以下直径</Button>
                </>
            );
        } },
    ]

    return (
        <div>
            <Space direction="vertical">
                {/* <Button type="primary" onClick={saveHandle}>保存</Button> */}
                <EditableTable
                    tableColumns={tableColumns} dataSource={tableData} setDataSource={setTableData}
                />
                <div className="modal-action">
                    <a className="btn btn-primary btn-sm" onClick={() => saveHandle()}>确定</a>
                    <a className="btn btn-sm" onClick={() => setVisible(0)}>取消</a>
                </div>
            </Space>
        </div>
    )
}

LensThicknessDiameterEdit.defaultProps = {
    labelObj: {},
    setlabelObj: (obj) => console.log(obj),
    saveLabelHandle: (labelObj) => console.log(labelObj),
    setVisible: num => console.log(num),
};

export { LensThicknessDiameterEdit, };
