import React, { useState, useEffect } from 'react';
import { Modal, Button, Space, Spin, Radio } from 'antd';

import {
    sphCylSelectTableColumnsTemplate,
    sphCylSelectTableDataTemplate,
    updateLableObjSphAndCyl,
} from '../../funcs/labelModels/lensSCSelectTableTemplate';

import EditableTable from '../../customComponents/editableTable';
// import { cookieGet, cookieSet, cookieRemove, } from '../../funcs/localSaveHandle/cookie_handle';
import { baseArrays, } from '../../custom_configs';

import { printLabelHandle, } from '../base_components/lodop/print_handle';

// 镜片批量打印组件

const LensBatchPrint = ({ labelObj, selectedPrinterIndex}) => {
    const [showDialog, setShowDialog] = useState(false);
    const [columns, setColumns] = useState([]);
    const [tableDataArr, setTableDataArr] = useState([]);
    const [openingLensModalButtonState, setOpeningLensModalButtonState] = useState(false);
    // console.log(columns, tableDataArr);

    const [maxCyl, setMaxCyl] = useState(200);
    const [sphAndCylSignIndex, setSphAndCylSignIndex] = useState(0)
    
    const [initingTableState, setInitingTableState] = useState(true);
    // const [multiplePrintingState, setMultiplePrintingState] = useState(false);

    // const saveLensPrintTableDataCookieKey = 'lensMultiplePrintTable';

    useEffect(() => {
        // 最大散光更改后操作
        initLensPrintTableColumns();
        initLensPrintNumbers();
    }, [maxCyl]);

    const sphCylSignObj = () => {
        // 选中的 sph cyl 符号
        return baseArrays.sphAndCylSigns[sphAndCylSignIndex];
    }

    const printSingleSphCylLensLable = async (currentLabelObj, sphString, cylInt, printNum) => {
        // 打印标签单个度数
        if (printNum > 0) {
            const cylString = sphCylSignObj().cyl + (Number(cylInt) / 100).toFixed(2).toString();
            // console.log(cylString);
            await updateLableObjSphAndCyl(currentLabelObj, sphString, cylString).then(newLabelObj => {
                // console.log(JSON.stringify(newLabelObj));
                printLabelHandle(selectedPrinterIndex, newLabelObj, 2, printNum);
            }).catch(err => {
                console.log(err);
            });
        }
    }

    const printLensSingleSphLabelsHandle = (printNumObj, printCountInt) => {
        // 打印单个近视度数的所有 cyl 镜片标签
        const sphString = printNumObj.sph ? sphCylSignObj().sph + printNumObj.sph : null;
        // console.log(sphString);
        if (!sphString) {
            return;
        }
        const lensSphItemKeys = Object.keys(printNumObj);
        for (let index = 0; index < lensSphItemKeys.length; index++) {
            const currentCylKey = lensSphItemKeys[index];
            if (!isNaN(Number(currentCylKey))) {
                // 是数字才打印, NaN跳过
                const currentPrintNum = Number(printNumObj[currentCylKey]);
                if (currentPrintNum > 0) {
                    // 打印当前标签
                    const currentLabelObj = { ...labelObj, };
                    const currentDeferSecond = printCountInt * 1000;
                    // console.log(`cyl is: ${currentCylKey}; Print number is: ${currentPrintNum}; Print count: ${printCountInt}`, );
                    setTimeout(() => {
                        // console.log(`deferTime: ${currentDeferSecond}; Sph: ${sphString}; Cyl: ${currentCylKey};`);
                        printSingleSphCylLensLable(currentLabelObj, sphString, Number(currentCylKey), currentPrintNum);
                    }, currentDeferSecond);
                    printCountInt = printCountInt + 1;
                }
            }
        }
        return printCountInt;
    }

    const lensLabelMultiplePrintHandle = () => {
        // 镜片标签批量打印
        setInitingTableState(true);
        const copyTableDataArr = [].concat(tableDataArr);
        let printCountIndex = 0;
        for (let index = 0; index < copyTableDataArr.length; index++) {
            const element = copyTableDataArr[index];
            printCountIndex = printLensSingleSphLabelsHandle(element, printCountIndex);
        }
        Modal.success({
            content: '批量打印完成!',
        });
        setInitingTableState(false);
    };

    // const savePrintTableDataHandle = () => {
    //     // 保存打印表格数据 js-cookie有长度现在暂时开发
    //     const tableString = JSON.stringify(tableDataArr);
    //     console.log(tableString);
    //     cookieSet(saveLensPrintTableDataCookieKey, tableString);
    //     // console.log(saveResult);
    //     cookieGet(saveLensPrintTableDataCookieKey).then(res => {
    //         console.log(res);
    //         message.success('保存成功!');
    //     }).catch(err => {
    //         console.log(err);
    //         message.error('保存失败...');
    //     })
    // };

    const initLensPrintTableColumns = () => {
        // 初始化镜片打印表格列
        sphCylSelectTableColumnsTemplate(maxCyl).then(res => {
            setColumns(res);
        }).catch(err => {
            // console.log('~~~~~~~~~~.....');
            console.log(err);
        });
    }

    const initLensPrintNumbers = () => {
        // 初始化镜片打印数量
        setInitingTableState(true);
        // console.log(sphCylSelectTableDataTemplate(maxCyl));
        setTimeout(() => {
            sphCylSelectTableDataTemplate(maxCyl).then(res => {
                setTableDataArr(res);
                setInitingTableState(false);
            }).catch(err => {
                console.log('~~~~~~......', err);
                setInitingTableState(false);
            });
        }, 500);
    }
    const showLensMultiplePrintModal = () => {
        // 显示镜片批量打印对话框
        setShowDialog(true);
        setOpeningLensModalButtonState(true);
        setTimeout(() => {
            // 暂停执行计算0.5秒，让对话框先显示
            initLensPrintTableColumns();
            initLensPrintNumbers();
        }, 500);
    }

    const closeLensMultiplePrintModal = () => {
        setOpeningLensModalButtonState(false);
        setShowDialog(false);
    }

    const selectMaxCylHandle = (e) => {
        setMaxCyl(e.target.value);
    }

    const MaxCylRadioComponent = () => {
        // 最大散光选择
        return (
            <div>
                最大散光:
                <Radio.Group onChange={selectMaxCylHandle} value={maxCyl}>
                    <Radio value={200}>200</Radio>
                    <Radio value={400}>400</Radio>
                    <Radio value={600}>600</Radio>
                </Radio.Group>
            </div>
        )
    };

    const selectSphAndCylSignHandle = (e) => {
        setSphAndCylSignIndex(e.target.value);
    }

    const SphAndCylSignRadioComponent = () => {
        // 最大散光选择
        return (
            <div>
                光度符号:
                <Radio.Group onChange={selectSphAndCylSignHandle} value={sphAndCylSignIndex}>
                    <Radio value={0}>-/-</Radio>
                    <Radio value={1}>-/+</Radio>
                    <Radio value={2}>+/-</Radio>
                    <Radio value={3}>+/+</Radio>
                </Radio.Group>
            </div>
        )
    };
  
    return (
        <>
            <Button onClick={showLensMultiplePrintModal} type="primary" shape="round" loading={openingLensModalButtonState}>镜片批量打印</Button>
            {/* <h3>镜片批量打印</h3> */}

            <Modal
                title="镜片批量打印"
                visible={showDialog}
                footer={null}
                width={1000}
                closable={false}
            >
                <Space direction="vertical">
                    <Space>
                        <Button onClick={lensLabelMultiplePrintHandle} type="primary" shape="round" loading={initingTableState}>批量打印</Button>
                        {/* <Button onClick={savePrintTableDataHandle} type="primary" shape="round">保存数量</Button> */}
                        <Button onClick={closeLensMultiplePrintModal} shape="round">关闭</Button>
                        <Button onClick={initLensPrintNumbers} shape="round" loading={initingTableState}>清除数量</Button>
                    </Space>
                    <hr></hr>
                    <MaxCylRadioComponent />
                    <SphAndCylSignRadioComponent />
                </Space>
                <Spin size="large" spinning={initingTableState} tip="Loading...">
                    <div style={{ width: '100%', height: '100%', overflow: 'auto', marginTop: '15px'}}>
                        <EditableTable
                            tableColumns={columns}
                            setDataSource={setTableDataArr}
                            dataSource={tableDataArr}
                            tableSize="small"
                        />
                    </div>
                </Spin>
            </Modal>
        </>
    )
}

export default LensBatchPrint;
