import React, {
  useContext, useState, useEffect, useRef,
} from 'react';
import {
  Table, InputNumber, Input, Form,
} from 'antd';

// 自定义可编辑表格 组件

const EditableContext = React.createContext();

const EditableRow = ({ index, ...props }) => {
  const [form] = Form.useForm();
  return (
    <Form form={form} component={false}>
      <EditableContext.Provider value={form}>
        <tr {...props} />
      </EditableContext.Provider>
    </Form>
  );
};

const EditableCell = ({
  title,
  editable,
  children,
  dataIndex,
  record,
  handleSave,
  inputType,
  ...restProps
}) => {
  /**
   * inputType: input/inputNumber
   */
  const [editing, setEditing] = useState(false);
  const inputRef = useRef();
  const form = useContext(EditableContext);
  useEffect(() => {
    if (editing) {
      inputRef.current.focus();
    }
  }, [editing]);

  const toggleEdit = () => {
    setEditing(!editing);
    form.setFieldsValue({
      [dataIndex]: record[dataIndex],
    });
  };

  const save = async () => {
    try {
      const values = await form.validateFields();
      // console.log(record, values);
      toggleEdit();
      handleSave({ ...record, ...values });
    } catch (error) {
      window.console.error('Save failed:', error);
    }
  };

  let childNode = children;

  // strInput 字符串或数字输入
  // console.log('InputType is ', inputType);
  const InputComponent = inputType === 'inputNumber' ? InputNumber : Input;

  if (editable) {
    childNode = editing ? (
      <Form.Item style={{ margin: 0, }} name={dataIndex}>
        <InputComponent
          ref={inputRef}
          onPressEnter={save}
          onBlur={save}
        />
      </Form.Item>
    ) : (
      <div
        className="editable-cell-value-wrap"
        style={{ width: '100%', height: '100%', padding: 5, margin: 0, border: '0px solid red', }}
        onClick={toggleEdit}
      >
        {children}
      </div>
    );
  }

  return <td {...restProps}>{childNode}</td>;
};

const EditableTable = ({ tableColumns, dataSource, setDataSource, tableSize }) => {
  // props 传入 tableColumns, dataSource, setDatasource
  // tableColumns = [{title: '', dataIndex: '', width: 80, fixed: 'left', editable: true}]
  const handleSave = (row, rowIndex) => {
    const newData = [].concat(dataSource);
    const item = newData[rowIndex];
    newData.splice(rowIndex, 1, { ...item, ...row, });
    setDataSource(newData);
  };

  const components = {
    body: {
      row: EditableRow,
      cell: EditableCell,
    },
  };

  const columns = tableColumns.map((col, rowIndex) => {
    if (!col.editable) {
      return col;
    }

    return {
      ...col,
      onCell: (record, currentRowIndex) => ({
        record,
        editable: col.editable,
        dataIndex: col.dataIndex,
        title: col.title,
        handleSave: (rowObj) => handleSave(rowObj, currentRowIndex),
        inputType: col.inputType,
      }),
    };
  });

  return (
    <div>
      <Table
        size={tableSize}
        components={components}
        rowClassName={() => 'editable-row'}
        bordered
        dataSource={dataSource}
        columns={columns}
        pagination={false}
        scroll={{ x: 'max-content', y: 260 }}
        tableLayout="fixed"
        size="small"
      />
    </div>
  );
}

EditableTable.defaultProps = {
  tableColumns: [],
  dataSource: [],
  setDataSource: (obj) => console.log(obj),
  tableSize: 'small',     // middle / small
};

export default EditableTable;
