const rotateAngles = [0, 90, 180, 270];

const inputStyle = (width = 14, marginLeft = 6, marginTop = 2, fontSize = 9, fontWeight = 'normal', angleIndex = 0) => {
  // input style
  const msRotateAngle = `rotate(${rotateAngles[angleIndex]}deg)`; // angleIndex= 0 1 2 3

  return {
    position: 'absolute',
    placeholder: '请输入文本',
    border: '0px solid grey',
    width: `${width}mm`,
    height: 'auto',
    marginLeft: `${marginLeft}mm`,
    marginTop: `${marginTop}mm`,
    fontSize: `${fontSize}pt`,
    fontWeight: fontWeight, // 'normal', 'bolder'
    transformOrigin: '0% 0%',
    transform: msRotateAngle,
    MsTransform: msRotateAngle,
    MozTransform: msRotateAngle,
    WebkitTransform: msRotateAngle,
    OTransform: msRotateAngle,
  };
};

const baseLabelInputModel = (values = '', databaseText = '', styleObj = {}) => {
  // 输入框模型
  // const width = styleObj.width ? parseInt(styleObj.width) : '26';
  // const marginLeft = styleObj.marginLeft ? parseInt(styleObj.marginLeft) : '6';
  // const marginTop = styleObj.marginTop ? parseInt(styleObj.marginTop) : '2';
  // const fontSize = styleObj.fontSize ? parseInt(styleObj.fontSize) : '9';
  const fontWeight = styleObj.fontWeight ? styleObj.fontWeight : 'normal';
  const angleIndex = styleObj.angleIndex ? styleObj.angleIndex : 0;

  const width = parseInt(styleObj.width);
  const marginLeft = parseInt(styleObj.marginLeft);
  const marginTop = parseInt(styleObj.marginTop);
  const fontSize = parseInt(styleObj.fontSize);

  return {
    delYes: true,
    inputValues: values,
    databaseText,
    style: inputStyle(width, marginLeft, marginTop, fontSize, fontWeight, angleIndex),
  };
};

const updateInputStyle = (sourceInputObj, updateStyle) => {
  // update input style 更新input样式
  const inputVal = sourceInputObj.inputValues ? sourceInputObj.inputValues : '';
  const databaseText = sourceInputObj.databaseText ? sourceInputObj.databaseText : '';
  const sourceStyle = sourceInputObj.style ? sourceInputObj.style : {};
  const styleUpdated = {
    ...sourceStyle,
    ...updateStyle
  };

  if (Object.keys(styleUpdated).indexOf('angleIndex') === -1) {
    const angleNum = parseInt(styleUpdated.transform.replace('rotate(', ''));
    styleUpdated.angleIndex = rotateAngles.indexOf(angleNum) > -1 ? rotateAngles.indexOf(angleNum) : 0;
  }

  const updatedLabelObj = baseLabelInputModel(inputVal, databaseText, styleUpdated);
  // 更新input style
  return updatedLabelObj;
}

