const commonLabelTemplateObject = () => {
  // 通用标签模版对象
  const labObj = {
    ...baseLabelModel,
  };
  labObj.labInputs = [];

  // 设置通用标签尺寸 75 * 25
  labObj.labWidth = 75;
  labObj.labHeight = 25;

  let commonLabNewInputObj = baseLabelInputModel('标签名称', 'title', {
      marginLeft: 4,
      marginTop: 19,
      angleIndex: 3,
      width: 28,
      fontSize: 10,
    });
  commonLabNewInputObj.indexNum = labObj.labInputs.length;
  labObj.labInputs.push(commonLabNewInputObj);
  commonLabNewInputObj = baseLabelInputModel('¥188', '', {
      marginLeft: 22,
      marginTop: 7,
      angleIndex: 1,
      width: 20,
      fontSize: 10,
    })
  commonLabNewInputObj.indexNum = labObj.labInputs.length;
  labObj.labInputs.push(commonLabNewInputObj);

  labObj.labQRCode = makeQrCode(60, 28, 5, 9, labObj.labWidth, labObj.labHeight);
  labObj.labBarcode = makeBarcode('', [2, 30], 30, 5, labObj.labWidth, labObj.labHeight);
  // console.log(labObj);
  return labObj;
};

const initInputObject = (width = 26, marginLeft = 6, marginTop = 2, fontSize = 9, fontWeight = 'normal', angleIndex = 0) => {
  // 初始化input
  if (marginLeft === 6 && marginTop === 2) {
    // 高和左边取随机数
    marginLeft = Math.ceil(Math.random()*10);
    marginTop = Math.ceil(Math.random()*10);
  }
  return {
    delYes: true,
    inputValues: '新建文本',
    databaseText: '',
    style: inputStyle(width, marginLeft, marginTop, fontSize, fontWeight, angleIndex),
  }
}

