const sphCylSelectTableDataTemplate = (maxCyl=600) => {

  return new Promise(async (resolve, reject) => {
    // 镜片度数数量选择表的 内容
    const sphArr = createDiopterArr(1200);
    const dataSource = [];
    for (let index = 0; index < sphArr.length; index += 1) {
      // console.log('looping...');
      await createLensTableSingleRowDataObj(sphArr, index, maxCyl).then(res => {
        dataSource.push(res);
      }).catch(err => {
        console.log(err);
      });
    }

    if (dataSource.length > 0) {
      resolve(dataSource);
    } else {
      console.log('Error code(dataSource)...');
      reject([]);
    }
  });
}

const queryThicknessOrDiameter = (labelObj, sph, thicknessOrDiameter) => {
  // 查询中心厚度或直径 thicknessOrDiameter = 'lensThickness' / 'lensDiameter'
  return new Promise((resolve, reject) => {
    const TDArray = labelObj.thicknessDiameter ? labelObj.thicknessDiameter : [];
    console.log(TDArray);
    let hasThicknessOrDiameter = 0;
    for (let index = 0; index < TDArray.length; index++) {
      const element = TDArray[index];
      if (Math.abs(parseFloat(element.sph)) === Math.abs(parseFloat(sph))) {
        hasThicknessOrDiameter += 1;
        const tDKey = thicknessOrDiameter.replace(/lens/, '').toLowerCase();
        let thicknessOrDiameterVal = element[tDKey] ? element[tDKey] : tDKey === 'thickness' ? '1.2' : 72;
        resolve(thicknessOrDiameterVal);
        return;
      }
    }
    if (hasThicknessOrDiameter === 0) {
      console.log(thicknessOrDiameter, ' None sph...');
      reject(-1)
    }
  });
}

const updateLableObjSphAndCyl = (labelObj, sph, cyl) => {
  // 更新标签sph和cyl, 中心厚度和直径
  // console.log(sph, cyl);
  return new Promise((resolve, reject) => {
    try {
      const copyLabelObj = { ...labelObj };
      const labInputs = [].concat(copyLabelObj.labInputs);
      for (let index = 0; index < labInputs.length; index++) {
        const item = labInputs[index];
        if (item.databaseText === 'sph') {
          item.inputValues = sph;
          // console.log(item);
        } else if (item.databaseText === 'cyl') {
          item.inputValues = cyl;
          // console.log(item);
        } else if (item.databaseText === 'thickness') {
          queryThicknessOrDiameter(labelObj, sph, 'lensThickness').then(currentThichness => {
            if (currentThichness > -1) {
              item.inputValues = currentThichness;
            }
          }).catch(err => {
            console.log(err);
          })
        } else if (item.databaseText === 'diameter') {
          queryThicknessOrDiameter(labelObj, sph, 'lensDiameter').then(currentDiameter => {
            if (currentDiameter > -1) {
              item.inputValues = currentDiameter;
            }
          }).catch(err => {
            console.log(err);
          });
        }
      }
      
      copyLabelObj.labInputs = labInputs;
      console.log(copyLabelObj);
      resolve(copyLabelObj);
      return;
    } catch (error) {
      console.log(error);
      reject({});
    }
  });
}

export {
  createDiopterArr,
  sphCylSelectTableColumnsTemplate,
  sphCylSelectTableDataTemplate,
  updateLableObjSphAndCyl,
};
