import React, {useEffect, useState} from 'react';
import { Button, Form, Input, Select, message } from 'antd';
import {
  FolderOpenOutlined,
  FileAddTwoTone,
  SaveTwoTone,
  PrinterTwoTone,
} from '@ant-design/icons';

import { printLabelHandle, } from '../base_components/lodop/print_handle';
import { ModalCreateLensLabel, } from '../modal_components/modal_create_label';
import { commonLabelTemplateObject, } from '../../funcs/labelModels/labelTemplateModels';
import { ModalLabelsList } from './modalLabelsList';

const { Option } = Select;

// labelObjUpdateCount={labelObjUpdateCount}
// setLabelObjUpdateCount={setLabelObjUpdateCount}

const CreateSelectPrintLabel = ({ labelObj, setLabelObj, labelObjUpdateCount, setLabelObjUpdateCount, commonOrLensLabel, selectedPrinterIndex, saveOrUpdatelabel }) => {
  // 新建或选择标签
  // commonOrLensLabel(标签种类) 0:通用标签, 1:镜片标签
  const [visible, setVisible] = useState(0);
  const [printNum, setPrintNum] = useState(1);
  const [previewOrPrint, setPreviewOrPrint] = useState(2);
  // const labCategory = commonOrLensLabel > 0 ? 1 : 0;

  // const [labelId, setLabelID] = useState(0);  // 标签ID, 0=不存在, 要新建

  const printLabelButtonStyle = {
    margin: '5px',
  };

  const createLabelHandle = () => {
    // 新建标签
    // setLabelID(0);
    if (commonOrLensLabel === 0) {
      //新建通用标签
      const labObj = commonLabelTemplateObject();
      console.log(labObj);
      setLabelObj(labObj);
      setTimeout(() => {
        setLabelObjUpdateCount(labelObjUpdateCount + 1);
      }, 1000);
    
    } else {
      // 新建镜片标签
      setVisible(1);
    }
  };

  const printLableHandle = () => {
    // 打印标签
    // console.log(labelObj);
    // console.log(selectedPrinterIndex);
    printLabelHandle(selectedPrinterIndex, labelObj, previewOrPrint, printNum, true);
  };

  const selectedLabelHandle = (labelObj) => {
    // 选中标签后操作
    setLabelObj(labelObj);
    setTimeout(() => {
      setLabelObjUpdateCount(labelObjUpdateCount + 1);
    }, 500);
  }

  return (
    <div>
      <Button style={printLabelButtonStyle} shape="round" icon={<FolderOpenOutlined />} size="middle"
        onClick={() => setVisible(2)}
      >选择标签</Button>
      <Button style={printLabelButtonStyle} shape="round" icon={<FileAddTwoTone />} size="middle"
        onClick={createLabelHandle}>新建标签</Button>
      <Button style={printLabelButtonStyle}
        shape="round"
        icon={<SaveTwoTone />}
        size="middle"
        onClick={saveOrUpdatelabel}
      >保存标签</Button>
      <hr></hr>
      <Form
        layout="inline"
      >
        <Form.Item label="打印份数">
          <Input type="number" defaultValue={printNum} onChange={(e) => setPrintNum(parseInt(e.target.value) ? parseInt(e.target.value) : 1)} style={{ width: 100 }}></Input>
        </Form.Item>
        <Form.Item>
          <Select defaultValue={2} onChange={(num) => setPreviewOrPrint(num)} bordered={false}>
            <Option value={2}>直接打印</Option>
            <Option value={1}>打印预览</Option>
          </Select>
        </Form.Item>
        <Form.Item>
          <Button icon={<PrinterTwoTone />} size="middle" onClick={printLableHandle} type="ghost">打印</Button>
        </Form.Item>
      </Form>
      <ModalCreateLensLabel
        showVisibleNum={1}
        visible={visible}
        setVisible={setVisible}
        setLabelObj={setLabelObj}
        labelObjUpdateCount={labelObjUpdateCount}
        setLabelObjUpdateCount={setLabelObjUpdateCount}
      ></ModalCreateLensLabel>
      <ModalLabelsList
        visible={visible}
        setVisible={setVisible}
        showVisibleNum={2}
        selectedLabel={labelObj}
        setSelectedLabel={selectedLabelHandle}
        commonOrLensLabel={commonOrLensLabel}
      ></ModalLabelsList>
    </div>
  )
}

export default CreateSelectPrintLabel;
