import React, { useState, useEffect } from 'react';
import { Form, Space, Select, Button, Tooltip } from "antd";
import { ReloadOutlined } from '@ant-design/icons';
import { loadPrinter } from '../base_components/lodop/load_printer';
import { cookieGet, cookieSet } from '../../funcs/localSaveHandle/cookie_handle';
import { storageKeys } from '../../custom_configs';

const { Option } = Select;

const PrintSelect = ({ selectedPrinterIndex, setSelectedPrinterIndex }) => {
    const [printerArr, setPrinterArr] = useState(['espon', 'fush', 'printer1']);
    const [form] = Form.useForm();
    const [selectedPrinterName, setSelectedPrinterName] = useState('');
  
    const reloadPrinters = () => {
        // 重新读取打印机
        loadPrinter().then((printers) => {
            setPrinterArr(printers);
            // console.log(printers);
            cookieGet(storageKeys.latestSelectedPrinterKey).then(printerIndex => {
                let selectedIndex = 0;
                const savedPrinterIndex = Number(printerIndex);
                console.log(`Cookie saved printer index: ${savedPrinterIndex}`);
                if (!isNaN(savedPrinterIndex) && savedPrinterIndex > 0 && savedPrinterIndex < printers.length) {
                    // 先赋值当cookie保存打印机index
                    selectedIndex = savedPrinterIndex;
                }
        
                if(selectedPrinterIndex > 0 && selectedPrinterIndex < printers.length) {
                    // 再赋值父组件传过来的打印机 index
                    selectedIndex = selectedPrinterIndex;
                }
                if (printers.length > 0) {
                    setSelectedPrinterName(printers[selectedIndex]);
                    setSelectedPrinterIndex(selectedIndex);
                }
            }).catch(err => {
                console.log(err)
            });
        }).catch(err => {
            console.log(err);
        });
    }
  
    const selectPrinterHandle = (printer) => {
      console.log(printer);
      const printerIndex = printerArr.indexOf(printer);
      setSelectedPrinterName(printer);
      cookieSet(storageKeys.latestSelectedPrinterKey, printerIndex.toString(), 180);
      setSelectedPrinterIndex(printerIndex);
    }
  
    useEffect(() => {
        // 组件加载时更新打印机数组
        reloadPrinters()
    }, [selectedPrinterIndex]);

    return (
      <Form layout="inline" form={form}>
        <Form.Item label="打印机(Printer)">
          <Space>
            <Select className="rounded min-w-min" onChange={selectPrinterHandle} value={selectedPrinterName}>
              {printerArr.map(printer => {
                return (<Option value={printer} key={printer}>{printer}</Option>)
              })}
            </Select>
            <Tooltip title="刷新打印机">
              <Button type="primary" shape="circle" icon={<ReloadOutlined />} onClick={reloadPrinters} />
            </Tooltip>
          </Space>
        </Form.Item>
      </Form>
    )
};

PrintSelect.defaultProps = {
    selectedPrinterIndex: 0,
    setSelectedPrinterIndex: num => console.log(num),
};

export { PrintSelect };
