import React, { useState, useEffect } from 'react';
import {
  Card, Form, InputNumber, Tooltip, Button, Switch, Modal, Space
} from 'antd';
import {
  ExclamationCircleOutlined, PlusCircleTwoTone, DeleteTwoTone, SyncOutlined
} from '@ant-design/icons';

// import { baseLabelInputModel } from '../../models/sourceStates';
import { baseLabelInputModel, initInputObject, updateInputStyle } from '../../funcs/labelModels/labelTemplateModels';
import { ZwBaseModal } from '../base_components/zw_components/zw_components';

const { confirm } = Modal;

const noneInputObj = baseLabelInputModel('noneInputSelected', '', {});

const InputEdit = ({ labelObj, setLabelObj, selectedInputObjIndex, updateInputObjVal, setEditVisible, labelObjUpdateCount, setLabelObjUpdateCount }) => {
  // 标签 input 内容格式编辑
  const [currentInputObj, setCurrentInputObj] = useState({...noneInputObj});
  const [deleteTipsVisible, setDeleteTipsVisible] = useState(0);

  const updateCurrentInputObj = () => {
    // 更新当前input对象
    // console.log(labelObj.labInputs);
    let labInputsNum = 0;
    if (typeof(labelObj.labInputs) !== 'undefined') {
      labInputsNum = labelObj.labInputs.length;
    }
    if (labInputsNum > selectedInputObjIndex && selectedInputObjIndex > -1) {
      setCurrentInputObj({...labelObj.labInputs[selectedInputObjIndex]});
    }else {
      setCurrentInputObj({ ...noneInputObj, });
    }
  }

  useEffect(() => {
    updateCurrentInputObj();
    // return () => {
    //   setCurrentInputObj({...noneInputObj});
    // };
  }, [selectedInputObjIndex, labelObj]);

  const addInputObj = () => {
    // 增加input文本框
    let updatedLableObj = { ...labelObj };

    updatedLableObj.labInputs.push(initInputObject());
    // console.log(updatedLableObj);
    setLabelObj(updatedLableObj);
  }

  const delInputObj = () => {
    // 删除选中的inputObj
    const deledLableObj = { ...labelObj };
    deledLableObj.labInputs.splice(selectedInputObjIndex, 1);
    // console.log(deledLableObj);
    setLabelObj(deledLableObj);
    setLabelObjUpdateCount(labelObjUpdateCount + 1);  // 更新标签显示
    updateCurrentInputObj();
    setDeleteTipsVisible(0);
    setEditVisible(0);

  }

  const changeAngle = () => {
    // 旋转input
    const anglesArr = [0, 90, 180, 270];
    const angleNum = parseInt(currentInputObj.style.transform.replace('rotate(', ''));
    let angleIndex = anglesArr.indexOf(angleNum);
    angleIndex = angleIndex < 0 ? 0 : angleIndex + 1;
    angleIndex = angleIndex > 3 ? 0 : angleIndex;
    // console.log(angleNum, angleIndex);
    const updatedInputObj = updateInputStyle(currentInputObj, { angleIndex: angleIndex, });
    // console.log(updatedInputObj);
    const copyLableObj = { ...labelObj };
    copyLableObj.labInputs.splice(selectedInputObjIndex, 1, updatedInputObj);
    setLabelObj(copyLableObj);
  }

  const checkInputFontWeight = (checked) => {
    // 文本框文字是否加粗
    console.log(checked);
    const fontWeightValArr = ['normal', 'bold'];
    let fontWeightArrIndex = checked ? 1 : 0;
    updateInputObjVal('fontWeight', fontWeightValArr[fontWeightArrIndex]);
  }
  let inputEditButtonStyle = {
    margin: '0 5px',
  }

  const CardInputEdit = () => {
    if (currentInputObj.inputValues === 'noneInputSelected') {
      return null;
    }
    return (
      <Card title={`文本框属性: ${currentInputObj.inputValues}`} extra={<a href="#">More</a>} style={{ width: 320 }}>
        <div style={{ width: '100%', height: '100%', overflow: 'auto', }}>
          <Form labelCol={{ span: 8 }} wrapperCol={{ span: 16 }}>
            <Form.Item label="字体大小">
              <InputNumber value={parseInt(currentInputObj.style.fontSize)} onChange={(num) => updateInputObjVal('fontSize', num)}></InputNumber>
            </Form.Item>
            <Form.Item label="字体加粗">
              <Switch checked={currentInputObj.style.fontWeight === 'bold'} onChange={checkInputFontWeight} />
            </Form.Item>
            <Form.Item label="文本框宽度">
              <InputNumber value={parseInt(currentInputObj.style.width)} onChange={(num) => updateInputObjVal('width', num)}></InputNumber>
            </Form.Item>
            <Form.Item label="文本框位置X:" style={{ marginBottom: 0 }}>
              <Space>
                  <InputNumber style={{ width: 80, }} min={-100} max={1000} value={parseInt(currentInputObj.style.marginLeft)} onChange={(num) => updateInputObjVal('marginLeft', num)}></InputNumber>
                  Y:
                  <InputNumber style={{ width: 80, }} min={-100} max={1000} value={parseInt(currentInputObj.style.marginTop)} onChange={(num) => updateInputObjVal('marginTop', num)}></InputNumber>
              </Space>
            </Form.Item>
          </Form>
        </div>
      </Card>
    );
  }

  return (
    <div className="bg-gray-200 bg-opacity-100">
      {/* <Tooltip title="新建文本框">
        <Button onClick={addInputObj} style={inputEditButtonStyle}
          type="default" shape="circle" icon={<PlusCircleTwoTone />} />
      </Tooltip> */}
      <Tooltip title="删除当前文本框">
        <Button onClick={() => setDeleteTipsVisible(1)} style={inputEditButtonStyle}
          type="default" shape="circle" icon={<DeleteTwoTone />} />
      </Tooltip>
      <Tooltip title="旋转文本框">
        <Button
          onClick={changeAngle} style={inputEditButtonStyle} type="default"
          shape="circle" icon={<SyncOutlined />} />
      </Tooltip>
      <hr></hr>
      <CardInputEdit></CardInputEdit>
      <ZwBaseModal title="删除标签文本" visible={deleteTipsVisible} setVisible={setDeleteTipsVisible} visibleNumber={1}>
        <div className="alert alert-warning">
          <div className="flex-1">
            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" className="w-6 h-6 mx-2 stroke-current"> 
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z"></path>                         
            </svg> 
            <label>删除标签文本后无法恢复!</label>
          </div>
        </div>
        <div className="modal-action">
          <a className="btn btn-primary btn-sm" onClick={delInputObj}>确定</a> 
          <a className="btn btn-sm" onClick={() => setDeleteTipsVisible(0)}>取消</a>
        </div>
      </ZwBaseModal>
    </div>
  );
}

export default InputEdit;
