import React, { useState, useEffect } from 'react';

import { labelStyle } from '../../funcs/styles/inputStyles.js';
import { computeElementScreenLocation } from '../../commmon_funcs/h5_element_handle';
import { QrcodeDiv, BarcodeDiv } from './code_div';
// import { ZwDivArrow, ZwBaseModal, ZwSvgQrcode, ZwCanvasQrcode } from '../base_components/zw_components/zw_components';

const LabelView = ({ labelObj, setLabelObj, labelObjUpdateCount, setLabelObjUpdateCount, visible, setVisible, visibleNumberQrcode, visibleNumberBarcode, updateSelectedInputObjIndex, updateInputObjVal, editState, setVisibleInputEditDiv, setInputEditDivLocation }) => {
  // 显示标签组件
  const [labInputsArr, setLabInputsArr] = useState([]);

  // const visibleLabelDiv = React.createRef();


  useEffect(() => {
    const newLabinputs = [].concat(labelObj.labInputs ? labelObj.labInputs : []);
    console.log(labelObj);
    setLabInputsArr(newLabinputs);
    // return () => {
    //   setLabInputsArr([]);
    // }
    // console.log(visibleLabelDiv.current.offsetLeft);
    // console.log(visibleLabelDiv.current.offsetTop);
  }, [labelObjUpdateCount]);

  const computeEditInputDivLocation = (inputIndex) => {
    // 计算编辑输入框的位置
    // getBoundingClientRect()方法。它返回一个对象，其中包含了left、right、top、bottom四个属性，分别对应了该元素的左上角和右下角相对于浏览器窗口（viewport）左上角的距离
    // const labelTop = visibleLabelDiv.current.getBoundingClientRect().top;
    // const labelLeft = visibleLabelDiv.current.getBoundingClientRect().left;
    // const selectedInputObj = labInputsArr[inputIndex];
    // console.log(selectedInputObj);
    let currentElement = null;
    if (document) {
      currentElement = document.getElementById(`labelTextInputs-${inputIndex}`);
    }
    console.log(currentElement);
    let elementLocatinObj = {};
    if (currentElement) elementLocatinObj = computeElementScreenLocation(currentElement);
    const top = elementLocatinObj.bottom ? elementLocatinObj.bottom + 10 : 120,
          left = elementLocatinObj.right ? elementLocatinObj.right + 10 : 260;
    console.log(top, left);
    return { top: top, left: left };

    // if (!selectedInputObj) return {top: labelTop, left: labelLeft};
    // const inputStyle = selectedInputObj.style ? selectedInputObj.style : {};
    // if (Object.keys(inputStyle).length < 1) return {top: labelTop, left: labelLeft};
    // const { marginTop, marginLeft, width } = inputStyle;
    // return {
    //   top: labelTop,
    //   left: labelLeft,
    // }
  };

  const selectedInputHandle = (inputIndex) => {
    // 选中input后操作
    updateSelectedInputObjIndex(inputIndex);
    const editLocation = computeEditInputDivLocation(inputIndex);
    console.log(editLocation);
    setInputEditDivLocation(editLocation);
    console.log(editState);
    if (editState) {
      console.log(editLocation);
      setVisibleInputEditDiv(2);
    }
  }
 
  return (
    <>
      <div style={labelStyle(labelObj)}>
        {
          labInputsArr.map((inputObj, index) => {
            return (
              <input
                id={`labelTextInputs-${index}`}
                key={index.toString()}
                value={typeof(inputObj) !== 'object' ? '' : inputObj.inputValues}
                style={typeof(inputObj) !== 'object' ? {backgroundColor: 'transparent'} : { ...inputObj.style, backgroundColor: 'transparent' }}
                onFocus={() => selectedInputHandle(index)}
                onChange={(event) => updateInputObjVal(index, event.target.value)} />
            )
          })
        }
        <QrcodeDiv labelObj={labelObj}
          setLabelObj={setLabelObj}
          labelObjUpdateCount={labelObjUpdateCount}
          setLabelObjUpdateCount={setLabelObjUpdateCount}
          visible={visible}
          setVisible={setVisible}
          visibleNumber={visibleNumberQrcode}
          editState={editState}
        ></QrcodeDiv>
        <BarcodeDiv
          labelObj={labelObj}
          setLabelObj={setLabelObj}
          labelObjUpdateCount={labelObjUpdateCount}
          setLabelObjUpdateCount={setLabelObjUpdateCount}
          visible={visible}
          visibleNumber={visibleNumberBarcode}
          setVisible={setVisible}
          editState={editState}
        ></BarcodeDiv>
      </div>
    </>
  )
};
LabelView.defaultProps = {
  labelObj: {},
  setLabelObj: labObj => console.log(labObj),
  labelObjUpdateCount: 0,
  setLabelObjUpdateCount: num => console.log(num),
  updateSelectedInputObjIndex: (i) => console.log(i),
  updateInputObjVal: (v) => console.log(v),
  editState: 1,
  setVisibleInputEditDiv: (num) => console.log(num),
  setInputEditDivLocation: (divLocationObj) => console.log(divLocationObj),
};

export default LabelView;
