import React, { useEffect, useState } from 'react';

// 全局正在读取

const OverallLoading = ({ visible, setVisible, visibleNumber }) => {
    const [visibleValue, setVisibleValue] = useState('invisible');

    useEffect(() => {
        setVisibleValue(visible === visibleNumber ? 'visible' : 'invisible')
    }, [visible]);

    // container fixed bg-gray-200 rounded-md w-1/5 h-1/5 z-auto block inset-1/3 mx-auto justify-center 

    return (
        <div className={`fixed container justify-center bg-gray-200 rounded-md opacity-50 w-1/5 h-1/5 z-auto block mx-auto inset-1/3 p-1 ${visibleValue}`}>
            <svg className="icon animate-spin absolute container justify-center" width="60px" height="60px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                <path d="M512 512m-426.666667 0a426.666667 426.666667 0 1 0 853.333334 0 426.666667 426.666667 0 1 0-853.333334 0Z" fill="#959BA7" fillOpacity=".3" />
                <path d="M938.666667 512c0-235.648-191.018667-426.666667-426.666667-426.666667v426.666667h426.666667z" fill="#008CD1" />
                <path d="M512 512m-320 0a320 320 0 1 0 640 0 320 320 0 1 0-640 0Z" fill="#FFFFFF" />
            </svg>
            <p className="absolute container justify-center left-1/3 bottom-0.5 text-sm">Loading...</p>
        </div>
    );
};

OverallLoading.defaultProps = {
    visible: 0,
    setVisible: val => console.log(val),
    visibleNumber: 1,
};

export { OverallLoading };
