import React, { useState, useEffect } from 'react';
import { Form, InputNumber } from 'antd';

const LabelSizeEdit = ({ labelObj, updateLabelObj }) => {
  const [labWidth, setLabWidth] = useState(0);
  const [labHeight, setLabHeight] = useState(0);

  useEffect(() => {
    setLabWidth(labelObj.labWidth);
    setLabHeight(labelObj.labHeight);
  }, [labelObj.labWidth, labelObj.labHeight]);

  const updateLabelSize = (widthHeight, val) => {
    // 更新标签尺寸 widthHeight= labWidth / labHeight
    let updatedLabelObj = { ...labelObj };
    updatedLabelObj[widthHeight] = val;
    updateLabelObj(updatedLabelObj);
  };

  return (
    <div>
      <Form>
        <Form.Item label="标签宽">
          <InputNumber value={labWidth} onChange={(num) => updateLabelSize('labWidth', num)} />
        </Form.Item>
        <Form.Item label="标签高">
          <InputNumber value={labHeight} onChange={(num) => updateLabelSize('labHeight', num)} />
        </Form.Item>
      </Form>
    </div>
  );
};

export default LabelSizeEdit;
