import { randomNum } from '../commmon_funcs/number_handle';

// 基本通用状态

// 标签基础对象
// lensDiameter: {'0.00-6.00': 72, '6.25-max': 70} 镜片直径
// lensThickness: 镜片中心厚度
// {'0.00-0.50': '2.0', '0.75-0.75': '1.8', '1.00-1.50': '1.5', '1.75-max': '1.2'}

const rotateAngles = [0, 90, 180, 270];

const computeAngleIndex = (styleObj={}) => {
  // 计算样式中的 角度 序号
  let transform = styleObj.transform ? styleObj.transform : 'rotate(0deg)';
  const angleNum = parseInt(transform.replace('rotate(', ''));
  let angleIndex = rotateAngles.indexOf(angleNum);
  angleIndex = angleIndex < 0 ? 0 : angleIndex;
  return angleIndex;
}

const makeQrcodeStyle = (qrcodeSize=60, marginLeft=0, marginTop=0, labWidth=80, labHeight=50) => {
  // qrcode style
  // const msRotateAngle = `rotate(${rotateAngles[angleIndex]}deg)`; // angleIndex= 0 1 2 3

  if (marginLeft === 0 && marginTop === 0) {
    // qr code 位置随机数 如果都是 0 使用随机数
    marginLeft = randomNum(1, labWidth - 2 - qrcodeSize / 3);
    marginTop = randomNum(1, labHeight - 4 - qrcodeSize / 3);
  }
  return {
    position: 'absolute',
    // border: '0px solid grey',
    padding: 0,
    margin: 0,
    width: `min-content`,
    height: 'auto',
    marginLeft: `${marginLeft}mm`,
    marginTop: `${marginTop}mm`,
  };
};

const updateQrcodeStyle = (oldStyle={}, updateObj={}, labWidth=80, labHeight=50) => {
  // 更新 qrcode style
  // const updateKeys = ['width', 'marginLeft', 'marginTop', 'fontSize', 'fontWeight'];
  const mergeStyle = { ...oldStyle, ...updateObj };
  let qrcodeSize = 0;
  if (mergeStyle.width) {
    try {
      qrcodeSize = parseInt(mergeStyle.width);
    } catch (error) {
      console.error(error);
      qrcodeSize = '';
    }
  }
  let marginLeft = mergeStyle.marginLeft && parseInt(mergeStyle.marginLeft) ? parseInt(mergeStyle.marginLeft) : '',
      marginTop = mergeStyle.marginTop && parseInt(mergeStyle.marginTop) ? parseInt(mergeStyle.marginTop) : '';
  
  return makeQrcodeStyle(qrcodeSize, marginLeft, marginTop, labWidth, labHeight);
};

const makeQrCode = (qrcodeSize=60, marginLeft=0, marginTop=0, fontSize=7, labWidth=80, labHeight=50) => {
  // 创建二维码
  const styleQrCode = makeQrcodeStyle(qrcodeSize, marginLeft, marginTop, labWidth, labHeight)
  return {
    delYes: false,
    urlValues: "http://localhost:8000/",
    qrcodeTips: "",
    qrcodeSize: qrcodeSize,
    qrcodeHeight: 0,
    tipsFontSize: fontSize,
    databaseText: "qrcode",
    styleQrCode: styleQrCode,
    showQrcode: false,
  }
}

const makeBarcodeStyle = (barcodeSize=[2, 50], marginLeft=0, marginTop=0, labWidth=80, labHeight=50, angleIndex = 0) => {
  // make barcode style
  const msRotateAngle = `rotate(${rotateAngles[angleIndex]}deg)`; // angleIndex= 0 1 2 3
  if (marginLeft === 0 && marginTop === 0) {
    // qr code 位置随机数 如果都是 0 使用随机数
    marginLeft = randomNum(1, labWidth - 2 - barcodeSize[0]);
    marginTop = randomNum(1, labHeight - 4 - barcodeSize[1]);
  }
  return {
    position: 'absolute',
    // border: '0px solid grey',
    padding: 0,
    margin: 0,
    width: `min-content`,
    height: 'auto',
    marginLeft: `${marginLeft}mm`,
    marginTop: `${marginTop}mm`,
    transformOrigin: '0% 0%',
    transform: msRotateAngle,
    MsTransform: msRotateAngle,
    MozTransform: msRotateAngle,
    WebkitTransform: msRotateAngle,
    OTransform: msRotateAngle,
  };
}

const updateBarcodeStyle = (barcodeSize=[1, 50], oldStyle={}, updateObj={}, labWidth=80, labHeight=50, updateAngleIndex=-1) => {
  // 更新 barcode style (marginLeft, marginTop) 和 angleIndex>-1 更新angle
  console.log(oldStyle, updateObj);
  const mergeStyle = { ...oldStyle, ...updateObj };
  // console.log(mergeStyle)
  const marginLeft = mergeStyle.marginLeft && parseInt(mergeStyle.marginLeft) ? parseInt(mergeStyle.marginLeft) : '',
        marginTop = mergeStyle.marginTop && parseInt(mergeStyle.marginTop) ? parseInt(mergeStyle.marginTop) : '';

  let angleIndex = computeAngleIndex(mergeStyle);
  if (updateAngleIndex > -1) angleIndex = updateAngleIndex;   // updateAngleIndex > -1; 更新条形码角度

  return makeBarcodeStyle(barcodeSize, marginLeft, marginTop, labWidth, labHeight, angleIndex);
}

const updateBarcodeValues = (oldBarcodeObj={}, updateObj={}) => {
  // update barcode: 
  // update keys = ['urlValues', 'barcodeType', 'barcodeSize', 'style', 'showQrcode']
  const upKeys = ['urlValues', 'barcodeType', 'barcodeSize', 'style', 'showCode']
  const retObj = { ...oldBarcodeObj };
  for (const k in updateObj) {
    if (upKeys.indexOf(k) > -1) {
      retObj[k] = updateObj[k];
    }
  }
  return retObj;
};

const makeBarcode = (barcodeType='', barcodeSize=[1, 25], marginLeft=0, marginTop=0, labWidth=80, labHeight=50) => {
  // barcodeType = default: '128Auto'; ['128Auto(code128)', 'CODE128 A/B/C', 'EAN8','EAN13','EAN128A','EAN128B','EAN128C','UPC_A','UPC_E0','UPC_E1','Code39','MSI','Codabar']
  // barcodeSize = list: [width, height]
  const style = makeBarcodeStyle(barcodeSize, marginLeft, marginTop, labWidth, labHeight)
  return {
    delYes: false,
    urlValues: "123456",
    barcodeType: barcodeType,
    barcodeSize: barcodeSize,
    databaseText: "barcode",
    style: style,
    showCode: false,
  }
}

