// 创建标签 input 的样式方法

const labelStyle = (labelObj) => {
  // 标签样式
  const { labWidth, labHeight, unit } = labelObj;
  return {
    position: 'relative',
    padding: 0,
    width: `${labWidth}${unit}`,
    height: `${labHeight}${unit}`,
    border: '1px solid gray',
    borderRadius: '5px',
  };
};

const inputStyle = (inputObj) => {
  // 单个input 样式
  const {
    fontSize,
    fontWeight,
    inputWidth,
    // inputHeight,
    rotateAngle,
    maginX,
    maginY,
  } = inputObj;

  const msRotateAngle = `rotate(${rotateAngle}deg)`;

  return {
    position: 'absolute',
    border: '0px solid grey',
    width: `${inputWidth}mm`,
    height: 'auto',
    marginLeft: `${maginX}mm`,
    marginTop: `${maginY}mm`,
    fontSize: `${fontSize}pt`,
    fontWeight: fontWeight,
    transformOrigin: '0% 0%',
    transform: msRotateAngle,
    MsTransform: msRotateAngle,
    MozTransform: msRotateAngle,
    WebkitTransform: msRotateAngle,
    OTransform: msRotateAngle,
  };
};

export { labelStyle, inputStyle };
