import getLodop from './LodopFuncs';

import { barcodeTypes } from '../../../custom_configs';
import { makeQrCode, makeBarcode } from '../../../models/sourceStates';

/** lodop 打印操作 */

// const lodopFuncs = require('./LodopFuncs');

// const LODOP = getLodop();
const angleArr = [0, 270, 180, 90];

const computeAngle = (transform) => {
    // 计算文本角度
    let angle_num = transform ? transform : '0';
    angle_num = angle_num.replace('rotate(', '')
    // console.log(angle_num)
    angle_num = parseInt(angle_num) ? parseInt(angle_num) : 0;
    return -angle_num;
}

/** 设置打印样式 inputObj.style css样式 */
const setPrintStyle = (LODOP, inputObj) => {
    const style = inputObj.style;
    // console.log(style);
    const angle = computeAngle(style.transform);
    
    const fontSize = parseInt(style["fontSize"]) ? parseInt(style["fontSize"]) : 9;
    const fontName = style
    let bold_number = 0;
    if (style.fontWeight) {
        const boldsArr = ['bold', 'bolder'];
        bold_number = boldsArr.indexOf(style.fontWeight) > -1 ? 1 : 0;
    }
    // console.log("fontSize:", fontSize);
    LODOP.SET_PRINT_STYLE("Angle", angle);
    LODOP.SET_PRINT_STYLE("FontSize", fontSize);
    LODOP.SET_PRINT_STYLE("FontName", fontName);
    LODOP.SET_PRINT_STYLE("Bold", bold_number);
};

/** 
 * 增加二维码 labQRCode.styleQrCode css样式
 * LODOP.ADD_PRINT_BARCODE(Top, Left, Width, Height, CodeType, CodeValue)
 * CodeType:
 *      二维码(QRCode，PDF417),
 *      一维码(128A，128B，128C，128Auto，EAN8，EAN13，EAN128A，EAN128B，EAN128C), 
 */
const addBarCode = (LODOP, labQRCode, CodeType) => {
    if (!labQRCode.showQrcode) return;

    const styleQrCode = labQRCode.styleQrCode;
    const top = styleQrCode.marginTop,
          left = styleQrCode.marginLeft;
    const width = labQRCode.qrcodeSize + 'px';
    let height = labQRCode.qrcodeSize + 'px';
    if (CodeType !== 'QRCode' && labQRCode.qrcodeHeight) height = labQRCode.qrcodeHeight + 'px';

    const codeVal = labQRCode.urlValues;
    // console.log(labQRCode, top,left,width,height,CodeType,codeVal);
    LODOP.SET_PRINT_STYLEA(0,"QRCodeVersion",1);
    LODOP.ADD_PRINT_BARCODE(top,left,width,height,CodeType,codeVal);
}

const computeCodeLodopType = (barcodeType) => {

    for (let index = 0; index < barcodeTypes.length; index++) {
        const element = barcodeTypes[index];
        if (element.value === barcodeType) return element.lodopType;
    }
    return '128Auto';
}

const setBarcodeStyle = (LODOP, labBarode) => {
    // 设置条码打印样式
    const style = labBarode.style;
    // console.log(style);
    const angle = computeAngle(style.transform);

    LODOP.SET_PRINT_STYLE("Angle", angle);
    LODOP.SET_PRINT_STYLE('FontSize', 9);
    LODOP.SET_PRINT_STYLE('Bold', 0);
}

/** 
 * 增加一维码 labQRCode.styleQrCode css样式
 * LODOP.ADD_PRINT_BARCODE(Top, Left, Width, Height, CodeType, CodeValue)
 * CodeType:
 *      一维码(128A，128B，128C，128Auto，EAN8，EAN13，EAN128A，EAN128B，EAN128C), 
 */
const addSingleBarCode = (LODOP, labBarode) => {
    console.log(labBarode);
    if (!labBarode.showCode) return;
    const style = labBarode.style;
    const top = style.marginTop,
          left = style.marginLeft,
          barcodeSize = labBarode.barcodeSize;
    console.log(barcodeSize);
    const width = barcodeSize[0];
    let height = barcodeSize[1] + 'px';

    const codeType = labBarode.barcodeType;
    const lodopType = computeCodeLodopType(codeType);
    const codeVal = labBarode.urlValues;
    const codeWidth = (codeVal.toString().length * width);
    // LODOP.SET_PRINT_STYLEA(0, lodopType, width);
    console.log(top, left, codeWidth, height, lodopType, codeVal);
    setBarcodeStyle(LODOP, labBarode);
    LODOP.ADD_PRINT_BARCODE(top, left, codeWidth, height, lodopType, codeVal);
}

/** 增加打印内容 LODOP.ADD_PRINT_TEXT("top mm","left mm","Width mm","Height mm","新加文本"); */
const addPrintTexts = (LODOP, inputObj) => {
    // BottomMargin 为打印高度到下边距
    const texts = inputObj.inputValues ? inputObj.inputValues : '';  // 文本内容 Error input object = ''
    const style = inputObj.style ? inputObj.style : {};
    const top = style.marginTop ? style.marginTop : '2mm';
    const left = style.marginLeft ? style.marginLeft : '5mm';
    const width = parseInt(style.width) ? parseInt(parseInt(style.width) * 1.2) + 'mm' : 'RightMargin'; // 用RightMargin关键字转义为打印区域相对于纸张的“右边距”
    const height = parseInt(style.fontSize) ? parseInt(parseInt(style.fontSize) * 0.36) + "mm" : 'BottomMargin';  // 用BottomMargin关键字转义为打印区域相对于纸张的“下边距”
    // console.log(top, left, width, height, texts);
    LODOP.ADD_PRINT_TEXT(top, left, width, height, texts);
};

/** 设置标签大小打印方向 LODOP.SET_PRINT_PAGESIZE(1,2100,2970,""); */
const setLabelSize = (LODOP, labelObj) => {
    const width = labelObj.labWidth ? labelObj.labWidth + "mm" : "80mm";
    const heigth = labelObj.labHeight ? (parseInt(labelObj.labHeight) -2) + "mm" : "48mm";
    LODOP.SET_PRINT_PAGESIZE(1, width, heigth, "CreateCustomPage");
};

/** 打印标签 */
const printSingleLabel = (
    LODOP,
    labelObj,
    printerName,
    previewOrPrint = 1,
    copiesNum = 1
) => {
    // let labelName = labelObj.labelName;
    // console.log(labelObj);
    const inputsArr = labelObj.labInputs;
    const qrcodeObj = labelObj.labQRCode ? labelObj.labQRCode : makeQrCode();
    const barcodeObj = labelObj.labBarcode ? labelObj.labBarcode : makeBarcode();

    // LODOP.PRINT_INIT(labelName); // 初始化标签
    if (!LODOP) {
        console.log('打印机未连接...', previewOrPrint, copiesNum);
        alert('打印机未连接...');
        return;
    }
    LODOP.PRINT_INIT("");
    setLabelSize(LODOP, labelObj); // 设置标签页面大小

    // 循环添加inputs
    for (let index = 0; index < inputsArr.length; index++) {
        const inputObj = inputsArr[index];
        // console.log(inputObj);
        setPrintStyle(LODOP, inputObj); // 设置样式
        addPrintTexts(LODOP, inputObj); // 添加内容
    }
    addBarCode(LODOP, qrcodeObj, 'QRCode');     // 增加二维码
    addSingleBarCode(LODOP, barcodeObj);         // 增加一维码
    

    LODOP.SET_PRINTER_INDEX(printerName); // 指定打印机
    LODOP.SET_PRINT_COPIES(parseInt(copiesNum)); // 打印份数
    if (previewOrPrint === 1) {
        LODOP.PREVIEW(); // 打印预览
    } else {
        LODOP.PRINT(); // 直接打印
    }
};

const computeLineHeight = (lineStr) => {
    // 计算行高度
    const heightLinesNum = Math.ceil(lineStr.length / 16) + 1;
    // if (heightLinesNum < 1) {
    //     heightLinesNum = 1;
    // }
    return heightLinesNum * 5;
};

const computePageHeightOrLineTop = (dataList, lineIndex = -1) => {
    // 计算打印页面高度, 或行到顶端的高度(lineIndex===0, 表示页面高度)
    let pageHeight = 20;
    let lineStr = '';
    if (lineIndex === -1) {
        // 返回页面高度
        for (let index = 0; index < dataList.length; index++) {
            lineStr = dataList[index];
            const currentLineHeight = computeLineHeight(lineStr);
            pageHeight = pageHeight + currentLineHeight;
        }
        return pageHeight + 'mm';
    } else {
        // 返回行到顶部的高度
        for (let index = 0; index < lineIndex; index++) {
            lineStr = dataList[index];
            const currentLineHeight = computeLineHeight(lineStr);
            pageHeight = pageHeight + currentLineHeight;
        }
        return pageHeight + 'mm';
    }
};

const add80PrintTexts = (LODOP, texts, index = 0, isTitle = false, dataList = []) => {
    // 80热敏纸 添加文字 Top,Left,Width,Height,strContent
    const lineheight = computeLineHeight(texts) + 'mm';
    let top = computePageHeightOrLineTop(dataList, index);
    // console.log(top);
    if (isTitle) {
        top = '5mm';
    }
    LODOP.ADD_PRINT_TEXT(top, '3mm', '65mm', lineheight, texts);
};

const set80PrintStyle = (LODOP, isTitle = false) => {
    // 80热敏纸 添加样式
    const fontSize = isTitle ? 15 : 11;
    LODOP.SET_PRINT_STYLE("FontSize", fontSize);
}

/** 打印 80mm 热敏纸单 */
const print80Label = (
    LODOP,
    dataList = [],
    labelTitle = '订单',
    printerName = -1,
    previewOrPrint = 1,
    copiesNum = 1
) => {
    return new Promise((resolve, reject) => {
        try {
            const pageHeight = computePageHeightOrLineTop(dataList);
            const labelSizeObj = {
                lableWidth: '80mm',
                labelHeight: pageHeight
            }
            // LODOP.PRINT_INIT(labelName); // 初始化标签
            LODOP.PRINT_INIT("");
            setLabelSize(LODOP, labelSizeObj); // 设置标签页面大小

            set80PrintStyle(LODOP, true); // 设置标题样式
            add80PrintTexts(LODOP, labelTitle, 0, true, dataList); // 添加标题

            set80PrintStyle(LODOP, false); // 设置内容样式
            // 循环添加inputs
            for (let index = 0; index < dataList.length; index++) {
                const texts = dataList[index];
                add80PrintTexts(LODOP, texts, index, false, dataList); // 添加内容
            }

            LODOP.SET_PRINTER_INDEX(printerName); // 指定打印机 printerName = -1 表示系统默认打印机
            LODOP.SET_PRINT_COPIES(parseInt(copiesNum)); // 打印份数
            if (previewOrPrint === 1) {
                LODOP.PREVIEW(); // 打印预览
            } else {
                LODOP.PRINT(); // 直接打印
            }
            resolve(true);
        } catch (error) {
            reject(error);
        }
    })
}

const print80LabelHandle = (
    dataList = [],
    labelTitle = '订单',
    printerName = -1,
    previewOrPrint = 1,
    copiesNum = 1
) => {
    return new Promise(async (resolve, reject) => {
        var hasComplete = false;    // 变量,页面是否已经加载完成
        if (typeof document !== 'undefined') {
            hasComplete = document.readyState === 'complete';
        }

        if (!hasComplete) {
            alert('请等待页面加载完成...');
            reject('页面加载未完成...');
        } else {
            let LODOP;
            try {
                LODOP = getLodop();
                const [success, err] = await print80Label(LODOP, dataList, labelTitle, printerName, previewOrPrint, copiesNum)
                    .then(result => [result, null]).catch(err => [null, err]);
                if (success) {
                    resolve(success);
                } else {
                    reject(err);
                }
            } catch (error) {
                reject(error);
            }
        }
    })
}

export {
    printSingleLabel,
    print80LabelHandle
};