import React, {useState} from 'react';

import { ZwToggleLine } from '../base_components/zw_components/zw_components';

import { makeQrCode, makeBarcode, updateBarcodeValues } from '../../models/sourceStates';
// 是否打开 标签文件样式编辑 框，是否显示 二维码 和 条形码

const StateStyleEditBarQrCodeShow = ({
    labelObj,
    setLabelObj,
    labelObjUpdateCount,
    setLabelObjUpdateCount,
    editState,
    setEditState,
}) => {
    const [visibleQrcode, setVisibleQrcode] = useState(false);
    const [visibleBarcode, setVisibleBarcode] = useState(false);

    const qrcodeVisibleOrHidden = (qrcodeBool) => {
        // 显示或隐藏二维码
        // console.log(qrcodeBool);
        const copyLabObj = { ...labelObj };
        if (!copyLabObj.labQRCode) copyLabObj.labQRCode = makeQrCode();

        copyLabObj.labQRCode.showQrcode = qrcodeBool;
        setVisibleQrcode(qrcodeBool);
        // console.log(copyLabObj);
        setLabelObj(copyLabObj);
        setLabelObjUpdateCount(labelObjUpdateCount + 1);
    };

    const barcodeVisibleOrHidden = (barcodeBool) => {
        // 显示或隐藏条形码
        // console.log(qrcodeBool);
        const copyLabObj = { ...labelObj };
        if (!copyLabObj.labBarcode) copyLabObj.labBarcode = makeBarcode();

        const updatedBarcode = updateBarcodeValues(copyLabObj.labBarcode, {showCode: barcodeBool});
        copyLabObj.labBarcode = updatedBarcode;
        setVisibleBarcode(barcodeBool);
        // console.log(copyLabObj);
        setLabelObj(copyLabObj);
        setLabelObjUpdateCount(labelObjUpdateCount + 1);
    };

    return (
        <div className="flex flex-wrap content-start p-2 items-center">
            <div data-tip="选中状态, 点击标签文本,会弹出样式位置编辑窗口!" class="tooltip">
              <ZwToggleLine labelText="文本样式编辑:" checked={editState} setChecked={setEditState} width={10}></ZwToggleLine>
            </div>
            {/* <div>
              <ZwToggleLine labelText="显示二维码:" checked={visibleQrcode} setChecked={qrcodeVisibleOrHidden} width={10}></ZwToggleLine>
            </div>
            <div>
              <ZwToggleLine labelText="显示条形码:" checked={visibleBarcode} setChecked={barcodeVisibleOrHidden} width={10}></ZwToggleLine>
            </div> */}
        </div>
    )
};

export {
    StateStyleEditBarQrCodeShow,
};
