// H5 组件属性操作

const computeElementScreenLocation = (el) => {
    // 计算 element 在屏幕中的 上下左右坐标
    // getBoundingClientRect()方法。它返回一个对象，其中包含了left、right、top、bottom四个属性，分别对应了该元素的左上角和右下角相对于浏览器窗口（viewport）左上角的距离
    const elTop = el.getBoundingClientRect().top;
    const elLeft = el.getBoundingClientRect().left;
    const elBottom = el.getBoundingClientRect().bottom;
    const elRight = el.getBoundingClientRect().right;

    return {
        top: elTop,
        left: elLeft,
        bottom: elBottom,
        right: elRight,
    }
};

export {
    computeElementScreenLocation,
};
