import React, { useEffect, useState } from 'react';
import { ZwSvgQrcode, ZwSvgBarcode } from '../../images/svgs/zw_svgs';
import { makeQrCode, makeBarcode, updateBarcodeValues } from '../../models/sourceStates';
// 标签设置的按钮组

const LabelSetButtons = ({
    isLensLabel,
    labelObj,
    setLabelObj,
    labelObjUpdateCount,
    setLabelObjUpdateCount,
    visible,
    setVisible,
    editState,
    setEditState,
    addInputObj,
}) => {
    const [qrcodeDisplayState, setQrcodeDisplayState] = useState(false);
    const [barcodeDisplayState, setBarcodeDisplayState] = useState(false);

    useEffect(() => {
        const qrcodeObj = labelObj.labQRCode ? { ...labelObj.labQRCode } : {},
                barcodeObj = labelObj.labBarcode ? { ...labelObj.labBarcode } : {};

        const qrState = qrcodeObj.showQrcode ? qrcodeObj.showQrcode : false,
                barState = barcodeObj.showCode ? barcodeObj.showCode : false;
        setQrcodeDisplayState(qrState);
        setBarcodeDisplayState(barState);
    }, [labelObjUpdateCount]);

    const qrcodeVisibleOrHidden = (qrcodeBool) => {
        // 显示或隐藏二维码
        // console.log(qrcodeBool);
        const copyLabObj = { ...labelObj };
        if (!copyLabObj.labQRCode) copyLabObj.labQRCode = makeQrCode();

        copyLabObj.labQRCode.showQrcode = qrcodeBool;
        // console.log(copyLabObj);
        setLabelObj(copyLabObj);
        setLabelObjUpdateCount(labelObjUpdateCount + 1);
    };

    const barcodeVisibleOrHidden = (barcodeBool) => {
        // 显示或隐藏条形码
        // console.log(qrcodeBool);
        const copyLabObj = { ...labelObj };
        if (!copyLabObj.labBarcode) copyLabObj.labBarcode = makeBarcode();

        const updatedBarcode = updateBarcodeValues(copyLabObj.labBarcode, {showCode: barcodeBool});
        copyLabObj.labBarcode = updatedBarcode;
        // console.log(copyLabObj);
        setLabelObj(copyLabObj);
        setLabelObjUpdateCount(labelObjUpdateCount + 1);
    };

    return (
        <div>
            <div data-tip="编辑标签尺寸" className="tooltip p-2">
              <button className="btn btn-circle btn-info btn-sm" onClick={() => setVisible(11)}>
                <svg t="1629981089871" className="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3205" width="24" height="24"><path d="M853.333333 426.666667a42.666667 42.666667 0 0 1-42.666666-42.666667V273.664L273.664 810.666667H384a42.666667 42.666667 0 1 1 0 85.333333H213.333333a85.333333 85.333333 0 0 1-85.333333-85.333333v-170.666667a42.666667 42.666667 0 1 1 85.333333 0v110.336L750.336 213.333333H640a42.666667 42.666667 0 1 1 0-85.333333h170.666667a85.333333 85.333333 0 0 1 85.333333 85.333333v170.666667a42.666667 42.666667 0 0 1-42.666667 42.666667z" fill="#000000" p-id="3206"></path><path d="M128 213.333333a85.333333 85.333333 0 0 1 85.333333-85.333333h170.666667a42.666667 42.666667 0 0 1 0 85.333333H213.333333v170.666667a42.666667 42.666667 0 0 1-85.333333 0V213.333333zM896 810.666667a85.333333 85.333333 0 0 1-85.333333 85.333333h-170.666667a42.666667 42.666667 0 1 1 0-85.333333h170.666667v-170.666667a42.666667 42.666667 0 1 1 85.333333 0v170.666667z" fill="#9F9F9F" p-id="3207"></path></svg>
              </button>
            </div>
            {isLensLabel === 1 ? (
                <div data-tip="编辑镜片直径厚度" className="tooltip p-2">
                    <button className="btn btn-circle btn-info btn-sm" onClick={() => setVisible(12)}>
                        <svg t="1629981537297" className="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="7786" width="24" height="24"><path d="M512 85.333333A426.666667 426.666667 0 0 0 85.333333 512a426.666667 426.666667 0 0 0 426.666667 426.666667 426.666667 426.666667 0 0 0 426.666667-426.666667A426.666667 426.666667 0 0 0 512 85.333333m0 768a341.333333 341.333333 0 0 1-341.333333-341.333333 341.333333 341.333333 0 0 1 341.333333-341.333333 341.333333 341.333333 0 0 1 341.333333 341.333333 341.333333 341.333333 0 0 1-341.333333 341.333333m170.666667-213.333333v-85.333333H341.333333v85.333333l-128-128 128-128v85.333333h341.333334V384l128 128-128 128z" p-id="7787"></path></svg>
                    </button>
                </div>
            ) : null}
            
            {qrcodeDisplayState ? (
                    <div data-tip="隐藏二维码" className="tooltip p-2">
                        <button className="btn btn-circle btn-outline btn-sm" onClick={() => qrcodeVisibleOrHidden(false)}>
                            <ZwSvgQrcode width={24} height={24}></ZwSvgQrcode>
                        </button>
                    </div>
                ) : (
                    <div data-tip="显示二维码" className="tooltip p-2">
                        <button className="btn btn-circle btn-warning btn-sm" onClick={() => qrcodeVisibleOrHidden(true)}>
                            <ZwSvgQrcode width={24} height={24}></ZwSvgQrcode>
                        </button>
                    </div>
            )}

            {barcodeDisplayState ? (
                    <div data-tip="隐藏条形码" className="tooltip p-2">
                        <button className="btn btn-circle btn-outline btn-sm" onClick={() => barcodeVisibleOrHidden(false)}>
                            <ZwSvgBarcode width={24} height={24}></ZwSvgBarcode>
                        </button>
                    </div>
                ) : (
                    <div data-tip="显示条形码" className="tooltip p-2">
                        <button className="btn btn-circle btn-warning btn-sm" onClick={() => barcodeVisibleOrHidden(true)}>
                            <ZwSvgBarcode width={24} height={24}></ZwSvgBarcode>
                        </button>
                    </div>
            )}

            <div data-tip="增加文本" className="tooltip p-2">
              <button className="btn btn-circle btn-warning btn-sm" onClick={() => addInputObj(labelObj, setLabelObj, labelObjUpdateCount, setLabelObjUpdateCount)}>
                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" className="inline-block w-6 h-6 stroke-current">   
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 6v12 M6 12h12"></path>
                </svg>
              </button>
            </div>
        </div>
    )
}

export { LabelSetButtons };
