import { labelsApi } from '../../apis/server_apis';
import { sendAxiosRequest } from '../../apis/common_request';

// requests 请求

const labelTemplatesApis = [
    labelsApi.labelTemplatesGet,
    labelsApi.labelTemplatePost,
    labelsApi.labelTemplatePut,
    labelsApi.labelTemplateDelete,
];

const computeLabelTitle = labelObj => {
    // 计算标签标题
    let labelTitle = '';
    const inputsList = [].concat(labelObj.labInputs);
    for (let index = 0; index < inputsList.length; index++) {
        const element = inputsList[index];
        if (element.databaseText && element.databaseText === 'title') {
            labelTitle = element.inputValues;
            break;
        }
    }
    return labelTitle;
};

const makeLabelRequestObject = (labelObject, categray = '', isLens = 0) => {
    // 创建请求对象
    const reqObj = {
        label_title: computeLabelTitle(labelObject),
        product_categray: categray,
        is_lens: isLens,
        label_obj: labelObject,
    };
    console.log(reqObj);
    return reqObj;
};

const labelTemplatesGet = (page, queryFactor) => {
    // 获取标签列表
    return new Promise((resolve, reject) => {
        const api = labelTemplatesApis[0].api;
        const reqParams = { ...queryFactor, page: page };
        sendAxiosRequest(api, 'get', reqParams, {}).then(res => {
            resolve(res);
        }).catch(err => {
            reject(err);
        });
    });
};

const labelTemplateGet = (id) => {
    // 获取单个标签
    return new Promise((resolve, reject) => {
        const api = labelTemplatesApis[0].api + '/' + id;
        sendAxiosRequest(api, 'get', {}, {}).then(res => {
            resolve(res);
        }).catch(err => {
            reject(err);
        });
    });
};

const labelTemplatePost = (labelObject, categray = '', isLens = 0) => {
    // 新建标签
    return new Promise((resolve, reject) => {
        const api = labelTemplatesApis[1].api;
        sendAxiosRequest(api, 'post', {}, makeLabelRequestObject(labelObject, categray, isLens)).then(res => {
            resolve(res);
        }).catch(err => {
            reject(err);
        });
    });
};

const labelTemplatePut = (id, labelObject, categray = '', isLens = false) => {
    // 新建标签
    return new Promise((resolve, reject) => {
        const api = labelTemplatesApis[2].api + '/' + id;
        sendAxiosRequest(api, 'put', {}, makeLabelRequestObject(labelObject, categray, isLens)).then(res => {
            resolve(res);
        }).catch(err => {
            reject(err);
        });
    });
};

const labelTemplateDelete = (id) => {
    // 删除标签模版
    return new Promise((resolve, reject) => {
        const api = labelTemplatesApis[3].api + '/' + id;
        sendAxiosRequest(api, 'delete', {}, {}).then(res => {
            resolve(res);
        }).catch(err => {
            reject(err);
        });
    });
};

export {
    computeLabelTitle,
    labelTemplatesGet,
    labelTemplateGet,
    labelTemplatePost,
    labelTemplatePut,
    labelTemplateDelete,
};
