import React, { useState } from 'react';
import { Modal, Select, Form, Input, InputNumber, Switch } from 'antd';

import {
    lensLabelTemplateObject, commonLabelTemplateObject
} from '../../funcs/labelModels/labelTemplateModels';

import { siteStyles } from '../../custom_configs';

const { Option } = Select;

const ModalCreateLensLabel = ({ showVisibleNum, visible, setVisible, setLabelObj, labelObjUpdateCount, setLabelObjUpdateCount }) => {
    // 新建镜片标签
    const [createLabelObj, setCreateLabelObj] = useState({
        'multipleFocuseLens': false,           // 是否是多焦点镜片
        'goodsName': '1.56超薄树脂防蓝光',           // '品名',
        'ne': 1.553,                    // '折射率',
        'vd': 37,                       // '阿贝数',
        'diameter': 72,                 // '直径',
    });

    const nesArr = [1.500, 1.553, 1.601, 1.665, 1.701, 1.737];

    const updateCreateObj = (k, val) => {
        // 更新创建的值
        console.log(typeof(val));
        const updatedCreateLableObj = { ...createLabelObj };
        let useVal = val;
        if (typeof(val) === 'object') {
            try {
                if (val.target.hasOwnProperty('value')) {
                    useVal = val.target.value;
                } else if (val.target.hasOwnProperty('checked')){
                    useVal = val.target.checked;
                }
            } catch (error) {
                console.log(error);
            }
        }
        updatedCreateLableObj[k] = useVal;
        setCreateLabelObj(updatedCreateLableObj);
        console.log(updatedCreateLableObj);
    }
    
    const createNewLabel = () => {
        // 新建标签
        const createLensLabelObj = lensLabelTemplateObject(createLabelObj, createLabelObj.multipleFocuseLens);
        // const createLensLabelObj = commonLabelTemplateObject();
        // console.log(createLensLabelObj);
        setLabelObj(createLensLabelObj);
        setTimeout(() => {
            setLabelObjUpdateCount(labelObjUpdateCount + 1);
        }, 1000);
        setVisible(0);
    }

    return (
        <Modal
            title="镜片标签创建"
            visible={showVisibleNum === visible}
            onOk={createNewLabel}
            onCancel={() => setVisible(0)}
        >
            <Form {...siteStyles.formLayoutStyle} name="新建镜片标签" initialValues={createLabelObj}>
                <Form.Item label="多焦镜片" name="multipleFocuseLens" valuePropName="checked">
                    <Switch onChange={(val) => updateCreateObj('multipleFocuseLens', val)} />
                </Form.Item>
                <Form.Item label="折射率" name="ne">
                    <Select style={{ width: 120 }} onChange={(val) => updateCreateObj('ne', val)}>
                        {nesArr.map((item, index) => {
                            return (<Option value={item} key={'createLabelObj' + index}>{item}</Option>)
                        })}
                    </Select>
                </Form.Item>
                <Form.Item label="阿贝数" name="vd">
                    <InputNumber onChange={(val) => updateCreateObj('vd', val)}></InputNumber>
                </Form.Item>
                <Form.Item label="直径" name="diameter">
                    <InputNumber onChange={(val) => updateCreateObj('diameter', val)}></InputNumber>
                </Form.Item>
                <Form.Item label="品名" name="goodsName">
                    <Input onChange={(val) => updateCreateObj('goodsName', val)}></Input>
                </Form.Item>
            </Form>
        </Modal>
    )
};

ModalCreateLensLabel.defaultProps = {
    showVisibleNum: 1,
    visible: false,
    setVisible: (numberVal) => console.log(numberVal),
    setLabelObj: (newLabelObj) => console.log(newLabelObj),
    labelObjUpdateCount: 0,
    setLabelObjUpdateCount: c => console.log(c),
};

export {
    ModalCreateLensLabel,
};
