import getLodop from './LodopFuncs';
import { printSingleLabel } from './lodop_print_handle';
import { Modal } from 'antd';

const printLabelHandle = (printerIndex, labelObj, previewOrPrint = 1, copiesNum = 1, printFinshedTip = false) => {
    /** 
     * printerIndex: 打印机序号, labelObj: 标签数据, previewOrPrint: 1-预览, 2-直接打印, printPageNumber:打印份数
     * 打印标签 */
    const LODOP = getLodop();
    printSingleLabel(LODOP, labelObj, printerIndex, previewOrPrint, copiesNum)
    if (printFinshedTip) {
        Modal.info({
            content: '打印完成!',
        });
    }
}

export { printLabelHandle };
